import React from 'react';
import { ResponsivePie } from '@nivo/pie';
import { PieContainer, TextContainer, Info, InfoConfidence } from './styles';
import { Colors } from '../Colors';

const Pie = ({ confidenceInterval, height, data, infoData = '', endAngle = 360 }) => (
	<PieContainer style={{ height: height }}>
		<TextContainer>
			<InfoConfidence>{confidenceInterval}</InfoConfidence>
			<Info>{infoData}</Info>
		</TextContainer>

		<ResponsivePie
			data={data}
			margin={{ top: 0, right: 40, bottom: 0, left: 10 }}
			innerRadius={0.7}
			padAngle={0.7}
			cornerRadius={0}
			enableSlicesLabels={false}
			colors={[ Colors.selectedBreacump, Colors.white ]}
			borderWidth={3}
			borderColor={{ from: 'color', modifiers: [ [ 'darker', 0.2 ] ] }}
			radialLabelsSkipAngle={10}
			fit={false}
			radialLabelsTextXOffset={6}
			radialLabelsTextColor={Colors.BlackPie}
			radialLabelsLinkOffset={-24}
			radialLabelsLinkDiagonalLength={0}
			endAngle={endAngle}
			radialLabelsLinkHorizontalLength={24}
			radialLabelsLinkStrokeWidth={1}
			radialLabelsLinkColor={{ from: 'color' }}
			slicesLabelsSkipAngle={10}
			slicesLabelsTextColor={Colors.BlackPie}
			animate={true}
			motionStiffness={90}
			motionDamping={15}
			defs={[]}
			fill={[]}
		/>
	</PieContainer>
);

export default Pie;
