import styled from '@emotion/styled';
import { css } from '@emotion/core';
import { Colors } from '../Colors'
import { Rubik } from '../Fonts'

const start = css`
  height: 40px;
  border-radius: 5px;
  border: solid 1px ${Colors.green};
  background-color: transparent;
  transition: all ease 0.3s;
  cursor: pointer;
  font-size: 1em;
  padding: 5px 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: ${Colors.green};
  &:hover {
    color: white;
    background-color: ${Colors.green};
    svg {
      fill: ${Colors.white};
    }
  }
`

const help = css`
  margin-bottom: 2.5rem;
  margin-left: 20px;
  margin-right: 20px;
  height: 32px;
  border-radius: 7px;
  cursor: pointer;
  background-color: #f25c63;
  border-color: rgb(216, 216, 216) rgb(209, 209, 209) rgb(186, 186, 186);
  border-style: solid;
  border-width: 1px;
  padding: 1px 7px 2px;
  color: ${Colors.white};
  word-break: keep-all;
  font-family: ${Rubik};
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  &:hover {
    background-color: #ee2d36;
  }
`

const buttonStylesType = {
  start,
  help
}

const getBackgroungColor = (props) => {
  const {
    disabled,
    backgroundColor,
  } = props
  if (disabled) {
    return 'transparent'
  }

  if (backgroundColor) {
    return backgroundColor
  }

  return 'transparent'
}

export const CustomButton = styled.button`
  ${props => buttonStylesType[props.stylesType]};

  &:hover {
    color: ${props => props.disabled ? Colors.mirage : Colors.lightGreen};
    background-color: ${props => props.disabled ? getBackgroungColor(props) : (props.backgroundColorHover || props.backgroundColor)};

    border: ${props => props.disabled ? `1px solid ${Colors.mirage}` : `1px solid ${Colors.lightGreen}`};
    svg {
      path {
        fill: ${props => props.fillIconColorHover || Colors.white};
      }
    }
  }

  svg {
    path {
      fill: ${props => props.fillIconColor || Colors.white};
    }
  }
`
