export const menuItems = (stage, dataSource, connected, predictor) => [
	{
		label: 'Connection',
		key: 'config',
		active: stage === 'config',
		icon: {
			type: 'FontAwesome',
			name: 'satellite-dish'
		}
  },
  {
		label: 'Databases',
		key: 'databases',
		active: stage === 'databases' || stage === 'DatabasesPreview' || stage === 'DatabasesQuality',
		icon: {
			type: 'FontAwesome',
			name: 'database'
		},
		disabled: !connected,
		items: dataSource === null ? undefined : [ dataSource ]
	},
	{
		label: 'Datasets',
		key: 'datasources',
		active: stage === 'datasources' || stage === 'dataSourcePreview' || stage === 'dataSourceQuality',
		icon: {
			type: 'FontAwesome',
			name: 'cubes'
		},
		disabled: !connected,
		items: dataSource === null ? undefined : [ dataSource ]
	},
	{
		label: 'Predictors',
		key: 'predictors',
		active: stage === 'predictors' || stage === 'predictorView' || stage === 'predictorDataQuality',
		icon: {
			type: 'FontAwesome',
			name: 'parking'
		},
		disabled: !connected,
		items: predictor === null ? undefined : [ predictor ]
	},
	{
		label: 'Query',
		key: 'query',
		disabled: !connected,
		active: stage === 'query',
		icon: {
			type: 'FontAwesome',
			name: 'question'
		}
	}
];
