import React from 'react';
import { StatusText, LabelText } from './styles'
import PropTypes from 'prop-types';

const StatusBar = ({ isconnected, serverType, serverStatus }) => {
  const serverStatusText = serverType === 'local' ? serverStatus :  isconnected ? 'Connected' : 'Disconnected';
  return (
	<>
		<StatusText>Status:</StatusText>
    <LabelText serverStatusText>
			{serverStatusText}
		</LabelText>
	</>
)};

StatusBar.propTypes = {
  isconnected: PropTypes.bool,
  serverType: PropTypes.string,
  serverStatus: PropTypes.string
}

export default StatusBar;
