import React, { Component } from 'react';
import PropTypes from 'prop-types';
import MindsDB from 'mindsdb-js-sdk';
import { Dialog } from 'primereact/dialog';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { ProgressBar } from 'primereact/progressbar';
import './index.scss';

class UrlWin extends Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    onHide: PropTypes.func.isRequired
  };

  state = {
    name: '',
    url: '',
    importing: false,
    validUrl: true
  };

  onShow = () => {
    this.setState({
      name: '',
      url: '',
      importing: false,
      validUrl: true
    });
  }

  onAdd = async () => {
    const { name, url } = this.state;
    const ds = MindsDB.DataSource({ name });

    this.setState({ importing: true });
    try {
      await ds.uploadFromUrl(url);
      // window.growl.show({ severity: 'success', detail: 'Data source added' });
      const { onHide } = this.props;
      onHide(true);
    } catch (e) {
      // window.showError(e);
      this.setState({ importing: false });
    }
  };

  isValidUrl = (value) => {
    return /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/i.test(value);
  };

  setFieldValue = (e) => {
    if (e.target.name === 'url') {
      if (!this.isValidUrl(e.target.value)) {
        this.setState({
          validUrl: false
        });
        return;
      }
    }
    this.setState({
      [e.target.name]: e.target.value,
      validUrl: true
    });
  };

  render() {
    const {
      name, url, importing, validUrl
    } = this.state;
    const invalid = !name || !url || !validUrl;
    const header = importing ? 'Importing data source' : 'New data source from url';
    return (
      <Dialog
        visible={ this.props.visible }
        onShow={ this.onShow }
        onHide={ this.props.onHide }
        modal
        header={ header }
        className="minds-upload-source"

      >
        { importing
          ? <ProgressBar mode="indeterminate" />
          : (
            <div>
              <div className="p-inputgroup minds-inputgroup">
                <span className="p-inputgroup-addon minds-inputgroup-addon">Name:</span>
                <InputText
                  name="name"
                  placeholder="Name your new dataset"
                  onChange={ this.setFieldValue }
                />
              </div>
              <div className="p-inputgroup minds-inputgroup">
                <span className="p-inputgroup-addon minds-inputgroup-addon">URL:</span>
                <InputText
                  name="url"
                  placeholder="https://example.com/myFile.csv"
                  className={ !validUrl ? 'inputError' : '' }
                  onChange={ this.setFieldValue }
                />
              </div>
              {!validUrl && (
                <div className="errorText">Please enter a valid Url</div>
              )}
              <div className="separate-btns">
                <Button
                  className="minds-action-btn red"
                  type="button"
                  label={ (
                    <div className="icon-text">
                      <span>CANCEL</span>
                    </div>
                  ) }
                  onClick={ this.props.onHide }
                />
                <Button
                  className="minds-action-btn"
                  type="button"
                  label={ (
                    <div className="icon-text">
                      <span>ADD</span>
                    </div>
                  ) }
                  onClick={ this.onAdd }
                  disabled={ invalid }
                />
              </div>
            </div>
          )
        }
      </Dialog>
    );
  }
}

export default UrlWin;
