import { action, observable, runInAction, decorate } from "mobx";

class PredictorStore {
  data = {};

  loading = false;

  loaded = false;

  async loadPreview(predictor, params) {
    this.data = predictor;
    this.loading = true;
    try {
      const data = await predictor.load(params);
      runInAction(() => {
        this.data = data;
        this.loading = false;
        this.loaded = true;
      });
    } catch (e) {
      // window.showError(e);
      runInAction(() => {
        this.loading = false;
      });
    }
  }
}

decorate(PredictorStore, {
  data: observable,
  loading: observable,
  loaded: observable,
  loadPreview: action,
});

export default new PredictorStore();
