import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { observer } from 'mobx-react';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import * as firebase from 'firebase/app';
import 'firebase/auth';
import axios from 'axios';
import { Mixpanel } from '../../../utils/mixpanel';
import GitHubButton from './gitHubButton';
import connectionStore from '../../../store/connection';
import configStore from '../../../store/config';
import './index.scss';
import { LoginContainer, InputContainer, Title, ServerButtons, CreateAccountContainer, InputTitle, LogoutContainer } from './styles';

// const { shell } = require('electron');
// TODO it will better to move last email or github-username to connection store

@observer
class CloudAuth extends Component {
  static propTypes = {
    onConnect: PropTypes.func.isRequired,
    onSetLoading: PropTypes.func.isRequired,
    loading: PropTypes.bool.isRequired,
    connected: PropTypes.bool.isRequired
  };

  state = {
    stage: 'login',           // [registration|login|logged]
    status: 'disconnected',   // [disconnected|connected]
    email: '',
    password: '',
    confirmPassword: '',
    passwordVisible: false,
    confirmPasswordVisible: false,
    emailBlured: false,
    passwordBlured: false,
    confirmPasswordBlured: false
  };

  componentDidMount() {
    this.gitHubProvider = new firebase.auth.GithubAuthProvider();
    this.cloudApi = axios.create({
      baseURL: configStore.cloudUrl,
      timeout: 12000
    });

    const lastLogin = JSON.parse(localStorage.getItem('cloud_login_email') || '{"provider": "email", "login": ""}');
    const email = lastLogin.provider === 'email' ? lastLogin.login : '';
    if (this.props.connected === true) {
      this.setState({
        stage: 'logged',
        status: 'connected',
        email
      });
    } else {
      this.setState({
        stage: 'login',
        status: 'disconnected',
        email
      });
    }
  }

  componentDidUpdate(prevProps, _prevState) {
    const { connected } = this.props;
    if (prevProps.connected !== connected) {
      if (connected === true) {
        /* eslint-disable-next-line */
        this.setState({
          stage: 'logged',
          status: 'connected'
        });
      } else {
        /* eslint-disable-next-line */
        this.setState({
          stage: 'login',
          status: 'disconnected'
        });
      }
    }
  }

  passwordInputRef = null;

  confirmPasswordInputRef = null;

  signUpButtonRef = null;

  logInButtonRef = null;

  setFieldValue = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({ [name]: value });
  };

  getIdToken = () => new Promise((resolve, reject) => {
    firebase.auth()
      .currentUser
      .getIdToken(true)
      .then(
        (idToken) => resolve(idToken)
      ).catch((err) => reject(err));
  });

  cloudLogin = async (jwt) => {
    const response = await this.cloudApi.post('/api/login', { jwt });
    return response.data;
  };

  testPing = async (token) => {
    await this.cloudApi.get('/util/ping', { params: { apikey: token }, timeout: 180000 });
  };

  onGitHubLogin = () => {
    this.props.onSetLoading(true);
    firebase.auth()
      .signInWithPopup(this.gitHubProvider)
      .then((data) => {
        localStorage.setItem(
          'cloud_login_email',
          JSON.stringify({
            provider: 'github',
            login: data.additionalUserInfo.username
          })
        );
      })
      .then(this.establishConnect)
      .then(async (success) => {
        if (success) {
          Mixpanel.track('Connection > MindsDB Cloud');
          await this.awaitState({
            status: 'connected',
            stage: 'logged'
          });
        } else {
          await this.awaitState({
            status: 'disconnected'
          });
          this.props.onSetLoading(false);
        }
      })
      .catch(async (error) => {
        const errorMessage = error.message;
        // window.growl.show({
        //   severity: 'error',
        //   summary: '',
        //   detail: errorMessage
        // });
        await this.awaitState({
          status: 'disconnected'
        });
        this.props.onSetLoading(false);
      });
  };

  setPasswordInputRef = (r) => {
    this.passwordInputRef = r;
  };

  setConfirmPasswordInputRef = (r) => {
    this.confirmPasswordInputRef = r;
  };

  setSignUpButtonRef = (r) => {
    this.signUpButtonRef = r;
  };

  setLogInButtonRef = (r) => {
    this.logInButtonRef = r;
  };

  awaitState = (newState) => new Promise((resolve) => {
    this.setState(newState, resolve);
  });

  establishConnect = async () => {
    try {
      const jwt = await this.getIdToken();
      await this.awaitState({
        status: 'requesting access token'
      });
      const resp = await this.cloudLogin(jwt);
      await this.awaitState({
        status: 'waiting for MindsDB answer'
      });
      await this.testPing(resp.token);
      await this.awaitState({
        status: 'connected'
      });
      this.props.onConnect(resp);
      return true;
    } catch (error) {
      // window.growl.show({
      //   severity: 'error',
      //   summary: '',
      //   detail: 'Error occurred while connecting to cloud'
      // });
      await this.awaitState({
        status: 'disconnected'
      });
      return false;
    } finally {
      this.props.onSetLoading(false);
    }
  };

  onEnter = (e) => {
    const { stage } = this.state;
    if (e.keyCode === 13) {
      if (e.target.name === 'email') {
        this.passwordInputRef.element.focus();
      } else if (e.target.name === 'password' && stage === 'registration') {
        this.confirmPasswordInputRef.element.focus();
      } else if (e.target.name === 'password' && stage === 'login') {
        this.logInButtonRef.element.focus();
      } else if (e.target.name === 'confirmPassword') {
        this.signUpButtonRef.element.focus();
      }
    }
  };

  isEmailCorrect = () => {
    // eslint-disable-next-line no-useless-escape
    const emailRegexp = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
    return emailRegexp.test(String(this.state.email).toLowerCase());
  };

  isPasswordCorrect = () => {
    const { password } = this.state;
    if (
      password.length < 6
      || password.length > password.replace(' ', '').length
    ) {
      return false;
    }
    return true;
  };

  isPasswordsSame = () => this.state.password === this.state.confirmPassword;

  onLogin = () => {
    const {
      email,
      password
    } = this.state;

    if (this.isEmailCorrect() === false) {
      // window.growl.show({
      //   severity: 'error',
      //   summary: '',
      //   detail: 'email not valid'
      // });
      return;
    }

    if (this.isPasswordCorrect() === false) {
      // window.growl.show({
      //   severity: 'error',
      //   summary: '',
      //   detail: 'password not valid'
      // });
      return;
    }

    this.props.onSetLoading(true);
    firebase.auth()
      .signInWithEmailAndPassword(email, password)
      .then(async () => {
        // window.growl.show({ severity: 'success', detail: 'Success login' });
      })
      .then(this.establishConnect)
      .then(async (success) => {
        if (success) {
          await this.awaitState({
            stage: 'logged'
          });
          localStorage.setItem(
            'cloud_login_email',
            JSON.stringify({
              provider: 'email',
              login: email
            })
          );
        } else {
          this.props.onSetLoading(false);
          await this.awaitState({
            status: 'disconnected'
          });
        }
      })
      .catch(async (error) => {
        const errorMessage = error.message;
        // window.growl.show({
        //   severity: 'error',
        //   summary: '',
        //   detail: errorMessage
        // });
        this.props.onSetLoading(false);
        await this.awaitState({
          status: 'disconnected'
        });
      });
  };

  onSignUp = async () => {
    const {
      email,
      password
    } = this.state;

    if (this.isEmailCorrect() === false) {
      // window.growl.show({
      //   severity: 'error',
      //   summary: '',
      //   detail: 'email not valid'
      // });
      return;
    }
    if (this.isPasswordCorrect() === false) {
      // window.growl.show({
      //   severity: 'error',
      //   summary: '',
      //   detail: 'password not valid'
      // });
      return;
    }
    if (this.isPasswordsSame() === false) {
      // window.growl.show({
      //   severity: 'error',
      //   summary: '',
      //   detail: 'passwords not same'
      // });
      return;
    }

    this.props.onSetLoading(true);
    await this.awaitState({
      status: 'registering new user'
    });
    firebase.auth()
      .createUserWithEmailAndPassword(email, password)
      .then(async () => {
        // window.growl.show({ severity: 'success', detail: 'Registration completed' });
        await this.awaitState({
          stage: 'login'
        });
      })
      .then(this.establishConnect)
      .then(async (success) => {
        if (success) {
          await this.awaitState({
            stage: 'logged'
          });
        } else {
          this.props.onSetLoading(false);
          await this.awaitState({
            status: 'disconnected'
          });
        }
      })
      .catch(async (error) => {
        const errorMessage = error.message;
        // window.growl.show({
        //   severity: 'error',
        //   summary: '',
        //   detail: errorMessage
        // });
        this.props.onSetLoading(false);
        await this.awaitState({
          status: 'disconnected'
        });
      });
  };

  togglePasswordVisible = () => {
    this.setState((s) => ({
      passwordVisible: !s.passwordVisible
    }));
  };

  toggleConfirmPasswordVisible = () => {
    this.setState((s) => ({
      confirmPasswordVisible: !s.confirmPasswordVisible
    }));
  };

  setBlured = (e) => {
    const name = `${e.target.name}Blured`;
    this.setState({ [name]: true });
  };

  goToLoginStage = () => {
    this.setState({ stage: 'login' });
  };

  goToRegistrationStage = () => {
    this.setState({ stage: 'registration' });
  };

  goToGithub = (e) => {
    e.preventDefault();
    // shell.openExternal('https://github.com/mindsdb/mindsdb');
  }

  onDisconnect = () => {
    if (connectionStore.connected) {
      connectionStore.disconnect();
    }
  };

  render() {
    const {
      email,
      password,
      confirmPassword,
      stage,
      status,
      passwordVisible,
      confirmPasswordVisible,
      emailBlured,
      passwordBlured,
      confirmPasswordBlured
    } = this.state;
    const { loading } = this.props;
    let color = status;
    if (color !== 'connected' && color !== 'disconnected') {
      color = 'installing';
    }
    const lastLogin = JSON.parse(localStorage.getItem('cloud_login_email') || '{"provider": "email", "login": ""}');
    return (
      <div className="cloud-connection">
        <div className="input-block">
          <LoginContainer>
          <Title>
            MindsDB Cloud
          </Title>
          <div className='form-container'>
          {
            stage === 'logged' && (
              <div className="input-container">
                <InputTitle>Welcome to MindsDB Cloud</InputTitle>
                <span>{ lastLogin.login }</span>
              </div>
            )
          }
          {
            (stage === 'registration' || stage === 'login') && (
              <InputContainer>
                <span className="input-title">Email:</span>
                <input
                  name="email"
                  placeholder="youremail@youreprovider.com"
                  className={ `mdb-input ${
                    emailBlured && email.length > 0 && !this.isEmailCorrect() ? 'error-input' : ''
                  }` }
                  value={ email }
                  onKeyUp={ this.onEnter }
                  onChange={ this.setFieldValue }
                  onBlur={ this.setBlured }
                />
              </InputContainer>
            )
          }
          {
            (stage === 'registration' || stage === 'login') && (
              <InputContainer>
                <span className="input-title">Password:</span>
                <div className="input-password-block">
                  <InputText
                    name="password"
                    placeholder="Password"
                    className={ `mdb-input ${
                      passwordBlured && password.length > 0 && !this.isPasswordCorrect() ? 'error-input' : ''
                    }` }
                    ref={ this.setPasswordInputRef }
                    type={ passwordVisible ? 'text' : 'password' }
                    value={ password }
                    onKeyUp={ this.onEnter }
                    onChange={ this.setFieldValue }
                    onBlur={ this.setBlured }
                  />
                  <Button
                    icon={ passwordVisible ? 'far fa-eye' : 'far fa-eye-slash' }
                    tabIndex="-1"
                    onClick={ this.togglePasswordVisible }
                  />
                </div>
              </InputContainer>
            )
          }
          {
            stage === 'registration' && (
              <InputContainer>
                <span className="input-title">Confirm New Password:</span>
                <div className="input-password-block">
                  <InputText
                    name="confirmPassword"
                    placeholder="Password"
                    className={ `mdb-input ${
                      confirmPasswordBlured && confirmPassword.length > 0 && !this.isPasswordsSame() ? 'error-input' : ''
                    }` }
                    ref={ this.setConfirmPasswordInputRef }
                    type={ confirmPasswordVisible ? 'text' : 'password' }
                    value={ confirmPassword }
                    onKeyUp={ this.onEnter }
                    onChange={ this.setFieldValue }
                    onBlur={ this.setBlured }
                  />
                  <Button
                    icon={ confirmPasswordVisible ? 'far fa-eye' : 'far fa-eye-slash' }
                    tabIndex="-1"
                    onClick={ this.toggleConfirmPasswordVisible }
                  />
                </div>
              </InputContainer>
            )
          }
          {
            stage === 'registration' && (
              <CreateAccountContainer>
                <p>
                Already have a MindsDB Cloud account?
                <a href="#/" role="button" onClick={ this.goToLoginStage }>
                  &nbsp;Please Log In
                </a>
              </p>
              </CreateAccountContainer>
            )
          }
          <ServerButtons>
          {
            stage === 'registration' && (
              <div>
                <Button
                  type="button"
                  className="minds-action-btn"
                  label="SIGN UP"
                  ref={ this.setSignUpButtonRef }
                  icon={ loading ? 'pi pi-spin pi-spinner' : 'fas fa-address-card' }
                  onClick={ this.onSignUp }
                  disabled={ loading }
                />
                <GitHubButton
                  onClick={ this.onGitHubLogin }
                  label="SIGN UP WITH GITHUB"
                  disabled={ loading }
                />
              </div>
            )
          }
          {
            stage === 'login' && (
              <div>
                <Button
                  type="button"
                  className="minds-action-btn"
                  label="LOG IN"
                  ref={ this.setLogInButtonRef }
                  icon={ loading ? 'pi pi-spin pi-spinner' : 'fas fa-cloud' }
                  onClick={ this.onLogin }
                  disabled={ loading }
                />
                <GitHubButton
                  onClick={ this.onGitHubLogin }
                  disabled={ loading }
                />
              </div>
            )
          }


        </ServerButtons>
        <CreateAccountContainer>
        { stage === 'login' && (
          <p>
          Need a MindsDB Cloud account?
          <a href="#/" role="button" onClick={ this.goToRegistrationStage }>
            &nbsp;Create an account
          </a>
        </p>
        )}
        </CreateAccountContainer>
        { stage === 'logged' && (
          <LogoutContainer>
            <Button
              type="button"
              className="minds-action-btn red"
              label="Logout"
              onClick={ this.onDisconnect }
              icon="fas fa-ban"
              disabled={ loading }
            />

          <p>
          Enjoying MindsDB?
          <a href="#/" role="button" onClick={ this.goToGithub }>
            &nbsp;Star us on Github !
          </a>
        </p>
        </LogoutContainer>
      )}
        </div>
        </LoginContainer>
        </div>
      </div>
    );
  }
}

export default CloudAuth;
