import React, { Component } from 'react';
import { Column } from 'primereact/column';
import { observer } from 'mobx-react';
import { Button } from 'primereact/button';
import { Breadcrumbs } from '../ui/Breadcrumbs';
import { TitleBar } from '../ui/TittleBar'
import DataTable from '../ui/mindsDataTable';
import './index.scss';

import dataSourceStore from '../../store/dataSource';
import EmptyPage from "../common/emptyPage";

import navigation from '../../store/navigation';
import { Mixpanel } from '../../utils/mixpanel';

@observer
class DataSourcePreview extends Component {
  state = {
    thWidths: null,
    thHeight: null
  };

  componentDidMount() {
    this.resizeObserver = new ResizeObserver(this.onResize);
    this.mutationObserver = new MutationObserver(this.onResize);
    this.resizeObserver.observe(this.wrapperRef);
    this.mutationObserver.observe(this.wrapperRef, { childList: true, subtree: true });
  }

  componentWillUnmount() {
    this.resizeObserver.disconnect();
    this.mutationObserver.disconnect();
  }

  onResize = () => {
    // get cells widths
    const row = this.wrapperRef.querySelector('.p-datatable-row');
    let thWidths = null;
    let thHeight = null;
    if (row) {
      thWidths = new Array(row.children.length);
      for (let i = 0; i < row.children.length; i++) {
        thWidths[i] = row.children[i].getClientRects()[0].width;
      }
    }
    // get title height
    const header = this.wrapperRef.querySelector('.p-datatable-thead > tr');
    if (header) {
      thHeight = header.getBoundingClientRect().height;
    }
    this.setState({
      thWidths, thHeight
    });
  };

  goToDatasources = (e) => {
    e.preventDefault();
    e.stopPropagation();
    navigation.setStage('datasources');
  };

  onPageChange = ({ rows, page }) => {
    dataSourceStore.updatePage({ rows, page });
  };

  onDataSortChange = ({ sortField, sortOrder }) => {
    dataSourceStore.updateSort({ sortField, sortOrder });
  };

  onFilterChange = ({ filters }) => {
    dataSourceStore.updateFilter(filters);
  };

  render() {
    const { thWidths, thHeight } = this.state;

    const fields = dataSourceStore.fields.map((x) => {
      if (x.name.includes('.')){
        x.name = x.name.replace(/\./g, '_');
      } return x.name;
    });

    const dynamicColumns = fields.map((field, index) => (
      <Column
        key={ `field_${index}` }
        field={ field }
        headerStyle={{
          width: thWidths ? thWidths[index] : undefined,
          maxWidth: thWidths ? thWidths[index] : undefined
        }}
        header={ (
          <div className="minds-table-clip-title">
            <div><span data-title={ field }>{field}</span></div>
          </div>
        ) }
        filter
      />
    ));

    return (
      <div className="main-content">
        <div className="header">
          <div />
          <TitleBar>
            Datasource Preview
          </TitleBar>
          <Breadcrumbs
            goToDatasources={this.goToDatasources}
            dataSourceId={dataSourceStore.dataSourceId}
            actualSite='Datasources'
          />
        </div>
        <div
          className="main-content__body table-fit-scroll preview-datasource"
          ref={ (r) => { this.wrapperRef = r; } }
        >
          <DataTable
            ref={ (el) => { this.dt = el; } }
            value={ dataSourceStore.data }
            className="minds-table stripped card datasource-preview-table"
            headerHeight={ thHeight }
            paginator
            rowsPerPageOptions={ [] }
            lazy
            onPage={ this.onPageChange }
            onSort={ this.onDataSortChange }
            onFilter={ this.onFilterChange }
            sortField={ dataSourceStore.sort.sortField }
            sortOrder={ dataSourceStore.sort.sortOrder }
            rows={ dataSourceStore.rows }
            filters={ dataSourceStore.filters }
            totalRecords={ dataSourceStore.total }
            first={ dataSourceStore.first }
            loading={ dataSourceStore.loading }
            emptyMessage={
              <div>
                <EmptyPage
                  topMessage="No matching items"
                  bottomMessage="Empty Set"
                  customIcon="fas fa-surprise"
                  noneBorder
                />
              </div>
            }
          >
            { dynamicColumns }
          </DataTable>
          <Button
            type="button"
            className="minds-action-btn quality-position"
            label="QUALITY"
            onClick={ (e) => {
              e.preventDefault();
              e.stopPropagation();
              navigation.openDataQuality(dataSourceStore.dataSource);
              Mixpanel.track('Datasources > Quality');
            } }
          />
        </div>
      </div>
    );
  }
}

export default DataSourcePreview;
