import React from 'react';
import { ResponsiveBar } from '@nivo/bar';

const HorizontalBar = ({ data, keys, margin, colors = [ '#00b06d' ], height = 300, onMouseEnter = ()=> {}, onMouseLeave = ()=> {}, legendTop}) => {
	return (
		<div style={{ height: height }}>
			<ResponsiveBar
				data={data}
				keys={keys}
				indexBy="importance"
				margin={margin}
				padding={0.2}
				innerPadding={0}
				layout="horizontal"
				colors={colors}
				enableGridX={true}
				enableGridY={true}
				defs={[]}
				fill={[]}
				borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
				axisRight={null}
				enableSlicesLabels={false}
				axisTop={legendTop}
				axisBottom={{
					tickSize: 5,
					tickPadding: 5,
					tickRotation: 0,
					legend: '',
					legendPosition: 'middle',
					legendOffset: 32
				}}
				axisLeft={{
					tickSize: 4,
					tickPadding: 10,
					tickRotation: 0,
					legendPosition: 'middle',
					legendOffset: 0
				}}
				labelSkipWidth={12}
				labelSkipHeight={12}
				tooltip={(d) => `${d.id} : ${d.value}`}
				labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
				legends={[]}
				animate={true}
				enableLabel={false}
				motionStiffness={90}
				motionDamping={15}
				onMouseEnter={(_data, event) => onMouseEnter(_data, event)}
				onMouseLeave={(_data, event) => onMouseLeave(_data, event)}
			/>
		</div>
	);
};

export default HorizontalBar;
