import React from 'react';
import PropTypes from "prop-types";

import "./index.scss";

// const { shell } = require('electron');

const EmptyPage = ({ noneBorder, topMessage, customIcon, bottomMessage, isHasButton, customBtnPosition, inProgressText, inProgressLink, inProgressTextLink}) => {
  const openJira = (e) => {
    e.preventDefault();
    // shell.openExternal('https://mindsdb.atlassian.net/servicedesk/customer/portals');
  }

  const openInProgressPage = (e) => {
    e.preventDefault();
    // shell.openExternal(inProgressLink);
  }

  return (
  <div className={`empty-page-container ${noneBorder ? '' :'border-container'}`} >
    <div className="message">
      <strong>{topMessage}&nbsp;</strong>
      <i className={`fas ${customIcon ? customIcon : 'fa-user-astronaut'} `}></i>
      <strong className="text-width">{bottomMessage}</strong>
      <div className="more-information">
        {inProgressText && <p className="text-in-progress"><i className="fas fa-exclamation-triangle"></i>{inProgressText}</p>}
        <a href="#/" onClick={openInProgressPage} className="text-width">{inProgressTextLink}</a>
      </div>
    </div>
    {isHasButton && (
      <button className="btn-report" style={{margin: customBtnPosition}} onClick={openJira}>
        <i className="fas fa-bug" />
        Report{' '}
      </button>
    )
    }
  </div>
)};

EmptyPage.propTypes = {
  noneBorder: PropTypes.string,
  topMessage: PropTypes.string,
  customIcon: PropTypes.string,
  bottomMessage: PropTypes.string,
  customBtnPosition: PropTypes.string,
  isHasButton: PropTypes.bool,
}

EmptyPage.defaultProps = {
  noneBorder: '',
  topMessage: '',
  customIcon: '',
  bottomMessage: '',
  customBtnPosition: '',
  isHasButton: false,
};

export default EmptyPage;
