import React, { Component } from "react";
import PropTypes from "prop-types";
import Moment from "moment";

import { Button } from "primereact/button";

import MindsPanel from "../../common/mindsPanel";
import Pie from "../../ui/QueryPie";
import Loading from "../../ui/Loading";
import { Subtitle } from "../../ui/SubTitle";
import "./index.scss";

class QueryResult extends Component {
  static propTypes = {
    query: PropTypes.object.isRequired,
    index: PropTypes.number.isRequired,
    predictorsStore: PropTypes.object.isRequired,
  };

  static boxColumnTemplate = (rowData, column) => (
    <div className="query-variables-table__variable">
      {" "}
      {rowData[column.field]}{" "}
    </div>
  );

  render() {
    const { query, index, predictorsStore } = this.props;

    // let forceCharts = [];
    let headerSub;
    let donutChartRender = [];
    let explanation_text = [];
    let extra_insights_data = [];
    let important_missing_information_output = [];
    let isVisibleImportant_missing;
    let ifMissing;

    if (query.error !== true && query.fullResult) {
      Object.keys(query.fullResult).forEach((key, _i) => {
        // const rawData = query.fullResult[key][0]["confidence_influence_scores"];
        // const data = rawData["column_names"].map((name, index) => ({
        //   // ERROR Cannot read property 'column_names' of undefined
        //   name,
        //   value: rawData["confidence_variation_score"][index]
        // }));

        //query.fullResult[key].forEach(item => {
        let {
          predicted_value,
          confidence,
          extra_insights,
          explanation = [],
        } = query.fullResult[key];
        const {
          important_missing_information,
          confidence_interval,
        } = explanation;

        isVisibleImportant_missing = important_missing_information;
        ifMissing = extra_insights.if_missing;

        if (!isNaN(parseFloat(predicted_value))) {
          predicted_value = parseFloat(predicted_value).toFixed(2);
        }

        if (extra_insights) {
          extra_insights_data = extra_insights.if_missing.map((item, index) => (
            <>
              <p className="p-content-text" key={index}>
                If{" "}
                <span className="tag tag__default">{Object.keys(item)[0]}</span>{" "}
                was missing the model would have predicted{" "}
                <span className="tag tag__default">
                  {item[Object.keys(item)[0]]}
                </span>
              </p>
              <br />
            </>
          ));
        }

        if (explanation && important_missing_information) {
          important_missing_information.forEach((item) =>
            important_missing_information_output.push(
              <div className="add-info-item">
                <span className="tag tag__default">{item}</span>
              </div>
            )
          );
        }

        confidence = parseFloat((confidence * 100).toFixed(0));
        headerSub = `${key}: ${predicted_value} ${confidence}% confident`;
        const dataPie = [
          {
            id: "",
            label: "confidence",
            value: confidence,
            color: "hsl(250, 70%, 50%)",
          },
        ];
        const intervalExplanation =
          explanation && confidence_interval
            ? confidence_interval[0].toFixed(0) +
              " to " +
              confidence_interval[1].toFixed(0)
            : predicted_value;
        explanation_text.push(
          <p className="explanation-text">
            Using the values listed below, this model predicted{" "}
            <span className="tag tag__success">{key.split("_").join(" ")}</span>{" "}
            {confidence_interval && `be in range from `}
            {confidence_interval && (
              <span className="tag tag__success">
                {confidence_interval[0].toFixed(0) +
                  " - " +
                  confidence_interval[1].toFixed(0)}
              </span>
            )}{" "}
            with <span className="tag tag__success">{confidence}%</span>{" "}
            confidence.
          </p>
        );
        donutChartRender.push(
          <div className="donut-chart-content">
            <Subtitle>{key}</Subtitle>
            <Pie
              confidenceInterval={intervalExplanation}
              infoData={`${dataPie[0].value}% confident`}
              height={320}
              data={dataPie}
              endAngle={(dataPie[0].value * 360) / 100}
            />
          </div>
        );
        //});
      });
    }

    const tableData = Object.keys(query.when).map((key) => ({
      variable: key,
      value: query.when[key],
    }));

    return (
      <MindsPanel
        className="minds-block interactive-title query-card"
        toggleable
        header={
          <>
            <span className="width-column-1">
              {`From ${query.pridictorName} Get ${query.toGet.join(", ")}`}{" "}
            </span>
            <span className="width-column-2 label-color">{headerSub}</span>
          </>
        }
        sectionTitleName={`From ${query.pridictorName} Get ${query.toGet.join(
          ", "
        )}`}
      >
        <>
          {query.loading && <Loading />}

          {query.error && (
            <div className="error-msg">
              <i className="fas fa-exclamation-triangle" />
              <span>Error occurred while executing query</span>
            </div>
          )}

          {!query.loading && (
            <>
              <div className="row">
                <div className="column-q">
                  <div className="donut-chart-container">
                    {donutChartRender}
                  </div>
                  <div>
                    <div className="query-card__body_variables_title">
                      Explanation
                    </div>
                    {explanation_text}
                    <div className="simple-table-container-q">
                      {tableData.map((item, index) => {
                        return (
                          <div className="simple-row" key={index}>
                            <div className="simple-column">
                              <div className="column-content">
                                {item.variable}
                              </div>
                            </div>{" "}
                            <div className="simple-column">
                              <div
                                className={`column-content ${ item.variable !== "timestamp" ?  "" : "timestamp-width" }`}
                              >
                                {item.variable !== "timestamp"
                                  ? item.value
                                  : Moment(item.value).format("LLLL")}
                              </div>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
                <div className="column-q">
                  {isVisibleImportant_missing && (
                    <MindsPanel
                      className="minds-block interactive-title query-card"
                      header="What additional information would help most"
                      sectionTitleName="What additional information would help most"
                      toggleable
                    >
                      <p className="p-content-text">
                        Knowing the value for the following columns would likely
                        yield more accurate predictions.
                        <div className="add-info-contet">
                          {important_missing_information_output}
                        </div>
                      </p>
                    </MindsPanel>
                  )}
                  {ifMissing && (
                    <MindsPanel
                      className="minds-block interactive-title query-card"
                      header="Extra Insights About This Prediction"
                      sectionTitleName="Extra Insights About This Prediction"
                      toggleable
                    >
                      {extra_insights_data}
                    </MindsPanel>
                  )}
                </div>
              </div>
            </>
          )}

          <div className="query-card__footer">
            <Button
              type="button"
              className="minds-action-btn red"
              label="DELETE"
              onClick={(e) => {
                e.preventDefault();
                predictorsStore.deleteQuery(index);
              }}
            />
          </div>
        </>
      </MindsPanel>
    );
  }
}

export default QueryResult;
