import { action, observable, decorate } from 'mobx';

import dataSourceStore from './dataSource';
import DataSourceQualityStore from './dataSourceQuality';
import predictorStore from './predictor';

class NavigationStore {
  stage = 'empty'; // [datasources|config|predictors|predictorView|predictorDataQuality|query|empty]
  dataSource = null;
  predictor = null;
  queryPredictorName = null;
  predictorDataQualityCol = null;

  setStage(stage) {
    this.stage = stage;
    this.predictorDataQualityCol = null;
  }

  openDataPreview(dataSource) {
    dataSourceStore.setDataSource(dataSource);
    this.stage = 'dataSourcePreview';
    this.dataSource = dataSource;
    this.predictorDataQualityCol = null;
  }

  openDataQuality(dataSource, params) {
    DataSourceQualityStore.loadDataQuality(dataSource, params);
    this.stage = 'dataSourceQuality';
    this.dataSource = dataSource;
    this.predictorDataQualityCol = null;
  }

  openPredictor(predictor, params) {
    predictorStore.loadPreview(predictor, params);
    this.stage = 'predictorView';
    this.dataSource = null;
    this.predictor = predictor;
    this.predictorDataQualityCol = null;
  }

  openQuery(predictorName) {
    this.queryPredictorName = predictorName;
    this.stage = 'query';
    this.predictorDataQualityCol = null;
  }

  openPredictorDataQuality(col){
    this.stage = 'predictorDataQuality';
    this.predictorDataQualityCol = col;
  }
}

decorate(NavigationStore, {
  stage: observable,
  dataSource: observable,
  predictor: observable,
  queryPredictorName: observable,
  predictorDataQualityCol: observable,
  setStage: action,
  openDataPreview: action,
  openDataQuality: action,
  openPredictor: action,
  openQuery: action,
  openPredictorDataQuality: action,
});

export default new NavigationStore();
