import React from "react";
import PropTypes from "prop-types";
import HorizontalBar from "../../ui/HorizontalBar";

const ColumnImportance = (props) => {
  const { data } = props.predictorStore;
  const rawImportance = data["model_analysis"][0]["overall_input_importance"];
  // const meta = data['data_analysis']['input_columns_metadata'];
  const importance = rawImportance.x.map((x, i) => ({
    x,
    y: parseFloat(rawImportance.y[i].toFixed(2)),
  }));
  importance.sort((a, b) => a.y - b.y);
  const pieData = importance.map((ps) => {
    return {
      importance: ps.x,
      importanceColor: "hsl(253, 70%, 50%)",
      [ps.x]: ps.y,
    };
  });
  const barMargin = { top: 20, right: 50, bottom: 50, left: 150 };
  const legendTop = {
    tickSize: 5,
    tickPadding: 5,
    tickRotation: 0,
    legend: "",
    legendPosition: "middle",
    legendOffset: 32,
  };

  return (
    <>
      <strong>Column Importance</strong>
      <br />
      <HorizontalBar
        data={pieData}
        keys={importance.map((column) => column.x)}
        margin={barMargin}
        height={400}
        legendTop={legendTop}
      />
    </>
  );
};

ColumnImportance.propTypes = {
  predictorStore: PropTypes.object.isRequired,
};

export default ColumnImportance;
