import { action, observable, computed, runInAction, decorate } from "mobx";
import MindsDB from "mindsdb-js-sdk";

class PredictorsStore {
  loading = false;
  loaded = false;
  data = [];
  queries = [];

  loadPromise = new Promise(resolve => resolve());

  fields = [
    {
      field: "name",
      header: "NAME"
    },
    {
      field: "data_source",
      header: "DATASOURCE"
    },
    {
      field: "predict",
      header: "FIELDS"
    },
    {
      field: "status",
      header: "STATUS"
    },
    {
      field: "accuracy",
      header: "ACCURACY"
    }
  ];

  reset = () => {
    this.loading = false;
    this.loaded = false;
    this.data = [];
    this.queries = [];
  };

  addQuery({ when, toGet, pridictorName }) {
    const query = {
      index: this.queries.length,
      when,
      toGet,
      pridictorName,
      loading: true,
      fullResult: null
    };
    this.queries.push(query);
    return query;
  }

  deleteQuery(id) {
    const index = this.queries.findIndex(el => el.index === id);
    if (index !== -1) this.queries.splice(index, 1);
    this.queries = this.queries.map((el, index) => {
      el.index = index;
      return el;
    });
  }

  updateQuery(index, { fullResult, error }) {
    const query = this.queries[index];
    this.queries[index] = {
      ...query,
      fullResult,
      loading: false,
      error
    };
    return this.queries[index];
  }

  async getOptionColumns(index) {
    if (this.loaded === false && this.loading === false) {
      await this.load();
    } else if (this.loaded === false && this.loading === true) {
      await this.loadPromise;
    }
    if (this.data.length === 0) return [];
    const pridictor = this.data[index];
    if (pridictor.columns === null) {
      await pridictor.loadColumns();
    }
    return pridictor.columns.map((el, index) => ({
      ...el,
      value: index,
      id: index
    }));
  }

  get dropdownOptions() {
    return this.data
      .filter(predictor => predictor.status === "complete")
      .map((el, index) => ({ label: el.name, value: index }));
  }

  async load(params) {
    this.loading = true;
    let loadResolve;
    this.loadPromise = new Promise(resolve => {
      loadResolve = resolve;
    });

    try {
      const predictors = await MindsDB.predictors(params);
      console.log(predictors)
      runInAction(() => {
        this.data = predictors;
        this.loading = false;
        this.loaded = true;
        loadResolve();
      });
    } catch (e) {
      console.log(e)
      // window.showError(e);
      // runInAction(() => {
      //   this.loading = false;
      //   loadResolve();
      // });
    }
  }
}

decorate(PredictorsStore, {
  loading: observable,
  loaded: observable,
  data: observable,
  queries: observable,
  reset: action,
  addQuery: action,
  deleteQuery: action,
  updateQuery: action,
  load: action,
  dropdownOptions: computed,
});

export default new PredictorsStore();
