import { observable, action, runInAction, decorate } from 'mobx';

class DataSourceStore {
  dataSource = null;
  dataSourceId = null;
  fields = [];

  data = [];
  total = 0;
  rows = 200;
  first = 0;
  page = 0;
  sort = {};
  filters = {};

  loading = false;
  loaded = false;

  loadTimeout = null;

  reset = () => {
    this.loading = false;
    this.loaded = false;
    this.data = [];
    this.total = 0;
    this.rows = 200;
    this.first = 0;
    this.page = 0;
    this.sort = {};
    this.filters = {};  // {field: {value: x}}
  };

  updatePage = ({ rows, page }) => {
    this.rows = rows;
    this.page = page;
    this.load({ reload: true });
  };

  updateSort = ({ sortField, sortOrder }) => {
    this.sort = { sortField, sortOrder };
    this.load({ reload: true });
  };

  updateFilter = (filters) => {
    this.filters = Object.keys(filters).reduce((accum, fieldName) => {
      accum[fieldName] = {
        value: filters[fieldName].value
      };
      return accum;
    }, {});
    if (this.loadTimeout != null) {
      clearTimeout(this.loadTimeout);
    }
    this.loadTimeout = setTimeout(() => {
      this.loadTimeout = null;
      this.page = 0;
      this.load({ reload: true });
    }, 500);
  };

  setDataSource = (dataSource) => {
    this.reset();

    this.dataSource = dataSource;
    this.dataSourceId = dataSource.name;
    this.fields = dataSource.columns;
    this.load();
  };

  load = async ({ reload = false } = { reload: false }) => {
    if (this.loadTimeout != null) {
      clearTimeout(this.loadTimeout);
    }
    if (this.loaded && reload !== true) {
      return null;
    }
    this.loading = true;
    try {
      const params = this.getTableQueryParams();
      const data = await this.dataSource.loadData(params);
      runInAction(() => {
        for(let i in data.data){
          for(let key in data.data[i]){
              if(key.includes('.')){
              let newKey = key.replace(/\./g, '_');
              data.data[i][newKey]=data.data[i][key];
              delete data.data[i][key];
            }
          }
        }
        this.data = data.data;
        this.total = data.rowcount;
        this.first = this.page * this.rows;
        this.loading = false;
      });
    } catch (e) {
      // window.showError(e);
      runInAction(() => { this.loading = false; });
    }
  };

  getTableQueryParams = () => {
    const params = [
      { key: 'page[size]', value: this.rows },
      { key: 'page[offset]', value: this.page * this.rows },
      { key: 't', value: (new Date()).getTime() }
    ];
    Object.keys(this.filters).forEach((field) => {
      if (
        'value' in this.filters[field]
        && typeof this.filters[field].value === 'string'
        && this.filters[field].value.length > 0
      ) {
        params.push({
          key: `filter[${field}]`,
          value: this.filters[field].value
        });
      }
    });
    // TODO add sort
    // if (
    //   this.sort.sortField != null
    //   && this.sort.sortOrder != null
    // ) {
    // }
    return params;
  };
}

decorate(DataSourceStore, {
  dataSource : observable,
  dataSourceId : observable,
  fields : observable,
  data : observable,
  total : observable,
  rows : observable,
  first : observable,
  page : observable,
  sort : observable,
  filters : observable,
  loading : observable,
  loaded : observable,
  reset: action,
  updatePage: action,
  updateSort: action,
  updateFilter: action,
  setDataSource: action,
  load: action,
});

export default new DataSourceStore();
