import React from 'react';
import PropTypes from 'prop-types';
import { Dialog } from 'primereact/dialog';
import { Button } from 'primereact/button';
import './index.scss';

const DeleteWin = (props) => (
  <Dialog
    visible={ props.visible }
    onHide={ props.onHide }
    modal
    header="Delete Datasource"
    className="minds-upload-source"
  >
    <span>
      Are you sure want to delete datasource &apos;{ props.name }&apos;?
    </span>
    <div className="separate-btns">
      <Button
        className="minds-action-btn"
        type="button"
        label="CANCEL"
        onClick={ props.onHide }
      />
      <Button
        className="minds-action-btn red"
        type="button"
        label="DELETE"
        onClick={ () => { props.onDelete(props.name); } }
      />
    </div>
  </Dialog>
);

DeleteWin.propTypes = {
  visible: PropTypes.bool.isRequired,
  onHide: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  name: PropTypes.string
};

DeleteWin.defaultProps = {
  name: null
};

export default DeleteWin;
