import React, { Component } from 'react';
import PropTypes from 'prop-types';
import MindsDB from 'mindsdb-js-sdk';
import { Dialog } from 'primereact/dialog';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { ProgressBar } from 'primereact/progressbar';
import { Mixpanel } from '../../../utils/mixpanel';
import * as Sentry from '@sentry/browser';

import './index.scss';

class UploadWin extends Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    onHide: PropTypes.func.isRequired
  };

  state = {
    name: '',
    file: null,
    uploading: false,
    percentCompleted: 0
  };

  onShow = () => {
    this.setState({
      uploading: false,
      name: '',
      file: null
    });
  };

  onUpload = async () => {
    const { name, file } = this.state;
    const ds = MindsDB.DataSource({ name });

    this.setState({ uploading: true, percentCompleted: 0 }, async () => {
      try {
        await ds.upload(file, this.onUploadProgress);
        // window.growl.show({ severity: 'success', detail: 'Data source uploaded' });
        Mixpanel.track('File uploaded');
        const { onHide } = this.props;
        onHide(true, name);
      } catch (e) {
        // window.showError(e);
        Sentry.captureException(e);
        this.setState({ uploading: false });
      }
    });
  };

  onUploadProgress = (percentCompleted) => {
    this.setState({ percentCompleted });
  };

  onPickFile = (e) => {
    if (e.target.files && e.target.files.length) {
      const file = e.target.files[0];
      this.setState({ file });
    } else {
      this.setState({ file: null });
    }
  };

  setFieldValue = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  render() {
    const { name, file, uploading, percentCompleted } = this.state;
    const invalid = !name || !file;
    const header = uploading ? 'Uploading ...' : 'New Datasource';
    const emptyCls = file ? '' : 'minds-file-input-value__empty';
    const fileTitle = file ? file.name : 'Click Here to Choose a File.';
    return (
      <Dialog
        visible={ this.props.visible }
        onHide={ this.props.onHide }
        onShow={ this.onShow }
        modal
        header={ header }
        className="minds-upload-source"
      >
        { uploading
          ? <ProgressBar value={ percentCompleted } unit='% Analysing data' />
          : (
            <div>
              <div className="p-inputgroup minds-inputgroup">
                <span className="p-inputgroup-addon minds-inputgroup-addon">File:</span>
                <div style={{width:'100%'}}>
                  <div className={ `minds-file-input-value ${emptyCls}` }>{fileTitle}</div>
                  <input
                    title={ fileTitle }
                    placeholder="fileTitle"
                    type="file"
                    id="file-input"
                    className="minds-file-input mdb-input"
                    onChange={ this.onPickFile }
                  />
                </div>
              </div>
              <div className="p-inputgroup minds-inputgroup">
                <span className="p-inputgroup-addon minds-inputgroup-addon">Name:</span>
                <InputText
                  name="name"
                  className="minds-input mdb-input"
                  placeholder="Name Your New Datasource"
                  value={ this.state.name }
                  onChange={ this.setFieldValue }
                  maxLength="100"
                />
              </div>
              <div className="separate-btns">
                <Button
                  className="minds-action-btn red"
                  type="button"
                  label={ (
                    <div className="icon-text">
                      <span>CANCEL</span>
                    </div>
                  ) }
                  onClick={ this.props.onHide }
                />
                <Button
                  className="minds-action-btn"
                  type="button"
                  label={ (
                    <div className="icon-text">
                      <span>UPLOAD</span>
                    </div>
                  ) }
                  onClick={ this.onUpload }
                  disabled={ invalid }
                />
              </div>
            </div>
          )
        }
      </Dialog>
    );
  }
}

export default UploadWin;
