import React, { Component } from "react";
import PropTypes from "prop-types";
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import { observer } from "mobx-react";
import { Dialog } from "primereact/dialog";
import { get, sortBy } from 'lodash';
import { Mixpanel } from "../../../utils/mixpanel";

import EmptyPage from "../../common/emptyPage";
import SimpleTable from "../../common/simpleTable";
// import QueryCondition from "./queryCondition";

import "./index.scss";

@observer
class NewQuery extends Component {
  static propTypes = {
    predictorsStore: PropTypes.object.isRequired,
    predictorStore: PropTypes.object.isRequired,
    preselectedPredictorName: PropTypes.string,
    visible: PropTypes.bool.isRequired,
    onHide: PropTypes.func.isRequired
  };

  static defaultProps = {
    preselectedPredictorName: null
  };

  state = {
    predictor: 0,
    predictorFields: [],
    otherFields: [],
    conditions: [],
    checkedFields: []
  };

  async componentWillMount() {
    const { preselectedPredictorName } = this.props;
    const { predictorsStore } = this.props;
    if (!predictorsStore.loaded) {
      await predictorsStore.load();
    }
    const selectedIndex = predictorsStore.data.findIndex(
      x => x.name === preselectedPredictorName
    );

    if (selectedIndex === -1) {
      this.setState({
        predictor: selectedIndex,
        predictorFields: [],
        otherFields: []
      });
      return;
    }

    await this.setPredictor(selectedIndex);
  }

  setPredictor = async index => {
    const { predictorsStore, predictorStore } = this.props;

    const predictorFields = predictorsStore.data[index].predict;
    // get predictor column types
    await predictorStore.loadPreview(predictorsStore.data[index]);

    let allColumns = await predictorsStore.getOptionColumns(index);
    allColumns = allColumns.map(el => el.name);
    const otherFields = [];
    allColumns.forEach(el => {
      if (predictorFields.indexOf(el) === -1) otherFields.push(el);
    });

    this.setState({
      predictor: index,
      predictorFields,
      otherFields,
      conditions: []
    });
  };

  onChangePredictor = e => {
    this.setPredictor(e.target.value);
  };

  onRunQuery = async () => {
    const { predictor, conditions, predictorFields } = this.state;
    const { predictorsStore } = this.props;
    const predictorObj = predictorsStore.data[predictor];
    this.props.onHide();

    const when = conditions.reduce((result, cond) => {
      const column = cond.field;
      if (typeof column === "string" && column.length > 0) {
        result[column] = cond.value;
      }
      return result;
    }, {});
    const query = predictorsStore.addQuery({
      when,
      toGet: predictorFields,
      pridictorName: predictorObj.name
    });
    let fullResult;
    let error = false;
    try {
      fullResult = await predictorObj.queryPredict(when, null, 'new_explain');
      fullResult = fullResult[0];
    } catch (err) {
      fullResult = {};
      error = true;
    }
    predictorsStore.updateQuery(query.index, { fullResult, error });
    Mixpanel.track("Query > Run Query");
  };

  setFieldValue = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    const {
      predictorFields,
      predictor,
      checkedFields,
      conditions
    } = this.state;
    const { predictorsStore } = this.props;

    let allFields = [];
    if (predictor !== -1 && predictorsStore.data[predictor]) {
      const predictorData = predictorsStore.data[predictor]
      const columnsMetadata = get(predictorData, 'data_analysis.input_columns_metadata', [])

      allFields = predictorsStore.data[predictor].columns;

      if (allFields) {
        allFields = allFields.map(item => {
          const column = columnsMetadata.find(columnItem => columnItem.column_name === item.name)
          if (column) {
            item.importance_score = column.importance_score
          } else {
            item.importance_score = 0
          }

          return item
        })

        allFields = sortBy(allFields, ['importance_score']).reverse()
      }
    }

    const { visible, onHide } = this.props;

    const getPredictorFields = () => {
      if (allFields && allFields.length > 0) {
        return allFields
          .map(item => ({
            label: item.name,
            value: item.name,
            data_type: item.data_type,
            is_target_column: item.is_target_column,
            distribution: item.distribution,
            extraFieldValue: { field: item.name, value: null }
          }))
          .filter(item => !item.is_target_column);
      }

      return [];
    };

    const setSelectedFields = (e, option) => {
      if (e.checked === false) {
        option.extraFieldValue.value = null;
        let { checkedFields } = this.state;
        checkedFields = checkedFields.filter(
          checked => checked !== option.label
        );

        this.setState({ checkedFields });
        setConditions(option, true);
      }
    };

    const setConditions = (option, removeCondition) => {
      let { conditions } = this.state;
      const existingCondition = conditions.find(
        item => item.field === option.field
      );
      if (removeCondition) {
        conditions = conditions.filter(item => item.field !== option.label);
      } else {
        if (existingCondition && existingCondition.field) {
          conditions.map(item => {
            if (item.field === option.field) {
              return option;
            }

            return item;
          });
        } else {
          conditions.push(option);
        }
      }

      const newConditions = [];
      conditions.forEach(item => {
        if (item && item.value) {
          newConditions.push(item);
        }
      });

      this.setState({ conditions: newConditions });
    };

    const hasEmptyConditions = !conditions || conditions.length < 1;

    return (
      <Dialog
        visible={visible}
        onHide={onHide}
        onShow={this.onShow}
        modal
        header="New Query"
        className="minds-dialog"
        style={{ width: "500px" }}
      >
        {" "}
        {this.props.predictorsStore.dropdownOptions.length > 0 ? (
          <>
            <div className="minds-dialog__field">
              <label htmlFor="cb3" className="p-checkbox-label">
                From:
              </label>
              <Dropdown
                inputId="cb4"
                name="predictor"
                value={predictor}
                options={this.props.predictorsStore.dropdownOptions}
                onChange={this.onChangePredictor}
                placeholder="Select Predictor"
                className="mdb-select"
                style={{ minWidth: 160 }}
              />
            </div>
            <div className="minds-dialog__field">
              <label htmlFor="cb3" className="p-checkbox-label">
                Get:
              </label>
              <div className="grid-cell-centered-v">
                {predictorFields.join(", ")}
              </div>
            </div>

            <div className="minds-dialog__field">
              <label htmlFor="cb4" className="p-checkbox-label">
                Taking into account the following data:
              </label>
            </div>
            <SimpleTable
                name="queryFields"
                filter
                showExtraFields
                value={checkedFields}
                options={getPredictorFields()}
                onChange={setSelectedFields}
                onExtraFieldChange={setConditions}
              />

            <div className="new-query__bottom">
              <div className="separate-btns" style={{ marginTop: "24px" }}>
                <p />
                <div>
                  <Button
                    className="minds-action-btn lg-btn-spacing bold red"
                    type="button"
                    label={
                      <div className="icon-text">
                        <span>CANCEL</span>
                      </div>
                    }
                    onClick={onHide}
                  />
                  <Button
                    className="minds-action-btn lg-btn-spacing bold"
                    type="button"
                    label="RUN QUERY"
                    onClick={this.onRunQuery}
                    disabled={hasEmptyConditions || predictor === -1}
                  />
                </div>
              </div>
            </div>
          </>
        ) : (
          <>
            <EmptyPage
              topMessage="There are no Predictors created"
              customIcon="fa-flushed"
              isHasButton={false}
              noneBorder
            />

            <div className="new-query__bottom">
              <div className="separate-btns" style={{ marginTop: "24px" }}>
                <p />
                <div>
                  <Button
                    className="minds-action-btn lg-btn-spacing bold red"
                    type="button"
                    label={
                      <div className="icon-text">
                        <span>CANCEL</span>
                      </div>
                    }
                    onClick={onHide}
                  />
                </div>
              </div>
            </div>
          </>
        )}
      </Dialog>
    );
  }
}

export default NewQuery;
