import React, { useState, useEffect } from 'react';
import { Dialog } from 'primereact/dialog';
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import { Mixpanel } from "../../../utils/mixpanel";
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-sql';
import 'prismjs/components/prism-regex';
import MindsDB from 'mindsdb-js-sdk';

import './prism.scss';
import './styles.scss';
import { RowContainer, ColContainer, QueryContainer, ButtonsContainer} from './styles'

import { InputText } from '../../inputText';
import { Button } from 'primereact/button';


const CreateDB = ({ visible, onHide, isEdit }) => {
  const [code, setCode] = useState('');
  const [type, setType] = useState('');
  const [statusDatabase, setStatusDatabase] = useState('');
  useEffect(() => {
    setType(isEdit.db_name);
  }, [isEdit]);
  const createDataSet = async () => {
    const data = {
      query: code,
      integration_id: isEdit.database_name,
      name: isEdit.database_name,
    };
    await MindsDB.DataBase()
      .newDataset(data)
      .then((response) => {
        if (response.name === 'Error') {
          // window.growl.show({
          //   severity: 'error',
          //   detail: response.message,
          // });
        } else {
          Mixpanel.track("Databases > Create Dataset");
          // window.growl.show({
          //   severity: 'success',
          //   detail: 'Dataset Created',
          // });
        }
        onHide();
      });
  };
  const testDatabase = async () => {
    await MindsDB.DataBase()
      .check(isEdit)
      .then((response) => {
        setStatusDatabase(
          response.data ? 'Connection OK' : 'Connection Failed'
        );
      })
      .catch((error) => console.error(error));
  };
  return (
    <Dialog
      visible={visible}
      onHide={onHide}
      modal
      header="Create Dataset from Database"
      className="minds-dialog width-100"
      style={{ width: '750px' }}
    >
      <RowContainer>
        <ColContainer>
          <div className="minds-dialog__field">
            <label htmlFor="cb3" className="p-checkbox-label">
              Database
            </label>
            <InputText
              disabled={true}
              className="mdb-input"
              placeholder="Database"
              value={type}
            />
          </div>
        </ColContainer>

        <QueryContainer>
          <div className="minds-dialog__field">
            <label htmlFor="cb5" className="p-checkbox-label">
              Query:
            </label>
            <Editor
              className="code-box"
              value={code}
              onValueChange={(jsSample) => setCode(jsSample)}
              highlight={(jsSample) => highlight(jsSample, languages.sql)}
              placeholder="select * from users;"
              padding={10}
            />
          </div>
          <span>{statusDatabase}</span>
        </QueryContainer>
      </RowContainer>
      <ButtonsContainer>
        <Button
          className="minds-action-btn"
          type="button"
          label="TEST"
          onClick={testDatabase}
        />
        <Button
          className="minds-action-btn red"
          type="button"
          label="CANCEL"
          onClick={onHide}
        />
        <Button
          className="minds-action-btn"
          type="button"
          label="CREATE"
          onClick={createDataSet}
        />
      </ButtonsContainer>
    </Dialog>
  );
};

export default CreateDB;
