import React from "react";
import PropTypes from "prop-types";

import HorizontalBar from "../../ui/HorizontalBar";
import { Colors } from "../../ui/Colors";
import withContext from "../../../HOC/withContext";
import Pie from "../../ui/PredictorPie";
import "./index.scss";

const TrainingInfo = (props) => {
  const { context, dataPreparation, accuracy } = props;
  const nivoPars = [
    {
      importance: "Test",
      importanceColor: "hsl(253, 70%, 50%)",
      Test:
        dataPreparation["test_row_count"] +
        dataPreparation["validation_row_count"],
      Title: "Testing Subset",
      Description: `A Testing Subset is the dataset we use to check how well our trained
      model aligns with alternative data - with the data that was not used for training. In other words, we use it
      to check how well the model has "learned"`,
    },
    {
      importance: "Training",
      importanceColor: "hsl(253, 70%, 50%)",
      Training: dataPreparation["train_row_count"],
      Title: "Training Subset",
      Description: `A Training Subset is what we call
      the dataset used to teach our model, in other words, the dataset used for
      "learning"`,
    },
  ];
  const barMargin = { top: 20, right: 20, bottom: 20, left: 60 };
  const barColors = [Colors.selectedBreacump, Colors.primaryBar];
  const onMouseEnter = (_data, event) => {
    event.target.style.fill = Colors.hoverBar;
    context.sidebarContent.setAnyValue({
      title: { $set: _data.data.Title },
      paragraph: { $set: _data.data.Description },
      url: { $set: "" },
    });
  };

  const onMouseLeave = (_data, event) => {
    event.target.style.fill =
      _data.id === "Test" ? Colors.selectedBreacump : Colors.primaryBar;
      context.sidebarContent.setAnyValue({
        title: { $set: '' },
        paragraph: { $set: '' },
        url: { $set: '' }
      })
  };

  // const usedRowsPercent = Math.round(
  //   (dataPreparation["used_row_count"] / dataPreparation["total_row_count"]) *
  //     100
  // );
  const dataAcuracy = [
    {
      id: "",
      label: "confidence",
      value: accuracy,
      color: "hsl(250, 70%, 50%)",
    },
  ];

  // const dataTest = [
  //   {
  //     id: "",
  //     label: "confidence",
  //     value: usedRowsPercent,
  //     color: "hsl(250, 70%, 50%)",
  //   },
  // ];

  return (
    <>
      <div className="row">
        <div className="column">
          <div className="graph-container">
            <div className="accuracy-doughnut">
              <Pie
                confidenceInterval={`${parseFloat((accuracy * 100).toFixed(3))}%`}
                infoData={'Test'}
                infoDatatext2={'Accuracy'}
                height={210}
                data={dataAcuracy}
                endAngle={( accuracy * 100* 360 ) / 100}
                mtop={81}
                mleft={13}
                pieColor={Colors.selectedBreacump}
              />
            </div>
            <div className="summary-doughnut">
            {/* <Pie
                confidenceInterval={`${usedRowsPercent}%`}
                infoData={'Training'}
                infoDatatext2={'Accuracy'}
                height={160}
                data={dataTest}
                endAngle={( usedRowsPercent * 100* 360 ) / 100}
                mtop={55}
                mleft={-33}
                pieColor={Colors.primaryBar}
              /> */}
            </div>
          </div>
          <strong>Model General Accuracies</strong>
          <p>
            When blindsided the model predicted correctly{" "}
            <span className="tag tag__default">
              {`${parseFloat((accuracy * 100).toFixed(3))}`}%
            </span>{" "}
            of the times, this means the model is fairly accurate.
          </p>
        </div>
        <div className="column">
          <strong className="title-s">Dataset Spliting and Usage</strong>
          <div>
            <HorizontalBar
              data={nivoPars}
              keys={nivoPars.map((column) => column.importance)}
              margin={barMargin}
              colors={barColors}
              height={300}
              onMouseEnter={onMouseEnter}
              onMouseLeave={onMouseLeave}
              legendTop={null}
            />
          </div>
        </div>
      </div>
    </>
  );
};

TrainingInfo.propTypes = {
  trainingData: PropTypes.array,
  predictor: PropTypes.object,
  dataPreparation: PropTypes.object,
  accuracy: PropTypes.number.isRequired,
  goToPredictorDataQuality: PropTypes.func.isRequired,
};

TrainingInfo.defaultProps = {
  trainingData: [],
  predictor: {},
  dataPreparation: {},
};

export default withContext(TrainingInfo);
