import React from 'react';
import { DataTable } from 'primereact/datatable';
import classNames from 'classnames';
import Paginator from './mindsPaginator';
import TableBottomSelector from './tableBottomSelector';
import './index.scss';

class MindsDataTable extends DataTable {
	createPaginator(position, totalRecords, _data) {
		const className = `minds-navigator p-paginator-${position}`;

		const rows = this.getRows();

		const options = this.props.rowsPerPageOptions.map((name) => ({
			name,
			selected: name === rows
		}));

		const rangeSelector = (
			<TableBottomSelector
				options={options}
				onSelect={(option) => {
					const rows = parseInt(option.name, 10);
					this.onPageChange({
						first: 0,
						rows,
						page: 0,
						pageCount: Math.ceil(totalRecords / rows) || 1
					});
				}}
			/>
		);

		return (
			<Paginator
				first={this.getFirst()}
				rows={this.getRows()}
				pageLinkSize={this.props.pageLinkSize}
				className={className}
				onPageChange={this.onPageChange}
				template={this.props.paginatorTemplate}
				totalRecords={totalRecords}
				rowsPerPageOptions={null} // changed from {this.props.rowsPerPageOptions}
				currentPageReportTemplate={this.props.currentPageReportTemplate}
				leftContent={rangeSelector} // changed from {this.props.paginatorLeft}
				rightContent={this.props.paginatorRight}
				alwaysShow={this.props.alwaysShowPaginator}
			/>
		);
	}

	render() {
		const {
			resizableColumns,
			responsive,
			columnResizeMode,
			virtualScroll,
			scrollable,
			autoLayout,
			selectionMode,
			paginator,
			paginatorPosition,
			header,
			footer,
			reorderableColumns,
      loading,
      frozenHeaderColumnGroup,
      frozenFooterColumnGroup,
      headerColumnGroup,
      footerColumnGroup,
      tableStyle,
      tableClassName,
      id,
      headerHeight
    } = this.props;

		let value = this.processData();
		let columns = this.getColumns();
		let totalRecords = this.getTotalRecords(value);
		let className = classNames(
			'p-datatable p-component',
			{
				'p-datatable-responsive': responsive,
				'p-datatable-resizable': resizableColumns,
				'p-datatable-resizable-fit': resizableColumns && columnResizeMode === 'fit',
				'p-datatable-scrollable': scrollable,
				'p-datatable-virtual-scrollable': virtualScroll,
				'p-datatable-auto-layout': autoLayout,
				'p-datatable-hoverable-rows': selectionMode
			},
			this.props.className
		);
		let paginatorTop = paginator && paginatorPosition !== 'bottom' && this.createPaginator('top', totalRecords);
		let paginatorBottom = paginator && paginatorPosition !== 'top' && this.createPaginator('bottom', totalRecords);
		let headerFacet = header && <div className="p-datatable-header">{header}</div>;
		let footerFacet = footer && <div className="p-datatable-footer">{footer}</div>;
		let resizeHelper = resizableColumns && (
			<div
				ref={(el) => {
					this.resizerHelper = el;
				}}
				className="p-column-resizer-helper p-highlight"
				style={{ display: 'none' }}
			/>
		);
		let tableContent = null;
		let resizeIndicatorUp = reorderableColumns && (
			<span
				ref={(el) => {
					this.reorderIndicatorUp = el;
				}}
				className="pi pi-arrow-down p-datatable-reorder-indicator-up"
				style={{ position: 'absolute', display: 'none' }}
			/>
		);
		let resizeIndicatorDown = reorderableColumns && (
			<span
				ref={(el) => {
					this.reorderIndicatorDown = el;
				}}
				className="pi pi-arrow-up p-datatable-reorder-indicator-down"
				style={{ position: 'absolute', display: 'none' }}
			/>
		);
		let loader;

		if (loading) {
			loader = this.renderLoader();
		}

		if (Array.isArray(columns)) {
			if (scrollable) {
				this.frozenSelectionMode = this.frozenSelectionMode || this.getFrozenSelectionModeInColumn(columns);
				let frozenColumns = this.getFrozenColumns(columns);
				let scrollableColumns = frozenColumns ? this.getScrollableColumns(columns) : columns;
				let frozenView, scrollableView;

        if (frozenColumns) {
					frozenView = this.createScrollableView(
						value,
						frozenColumns,
						true,
						frozenHeaderColumnGroup,
						frozenFooterColumnGroup,
						totalRecords
					);
				}

				scrollableView = this.createScrollableView(
					value,
					scrollableColumns,
					false,
					headerColumnGroup,
					footerColumnGroup,
					totalRecords
				);

				tableContent = (
					<div className="p-datatable-scrollable-wrapper">
						{frozenView}
						{scrollableView}
					</div>
				);
			} else {
				let tableHeader = this.createTableHeader(value, columns, headerColumnGroup);
				let tableBody = this.createTableBody(value, columns);
				let tableFooter = this.createTableFooter(columns, footerColumnGroup);

				tableContent = (
					<div className="p-datatable-wrapper">
						<table
							style={tableStyle}
							className={tableClassName}
							ref={(el) => {
								this.table = el;
							}}
						>
							{tableHeader}
							{tableFooter}
							{tableBody}
						</table>
					</div>
				);
			}
		}

		return (
			<div id={id} className={className} style={this.props.style}>
				<div
					className="p-datatable-outer-wrapper"
					style={{
            paddingTop: headerHeight ?
            this.props.className.includes('stripped') ? headerHeight + 45 : headerHeight
            : 0
					}}
					ref={(el) => {
						this.container = el;
					}}
				>
					{loader}
					{headerFacet}
					{paginatorTop}
					{tableContent}
					{/* {paginatorBottom} */}
					{footerFacet}
					{resizeHelper}
					{resizeIndicatorUp}
					{resizeIndicatorDown}
				</div>
				{paginatorBottom}
			</div>
		);
	}
}

export default MindsDataTable;
