import React, { Component } from "react";
import PropTypes from "prop-types";
import { observer } from "mobx-react";

import TrainingInfo from "./trainingInfo";
import { Button } from "primereact/button";
import ColumnImportance from "./columnImportance";
import { wrapHistXY } from "../../utils/funcs";
import { Breadcrumbs } from "./../ui/Breadcrumbs";
import MindsPanel from "../common/mindsPanel";

// import ConfusionMatrix from "./confusionMatrix";
import { ConfMatrix } from "../ui/ConfusionMatrix";
import Loading from "../ui/Loading";
import { Mixpanel } from "../../utils/mixpanel";
import navigation from "../../store/navigation";

import ErrorBoundary from "../common/utils/ErrorBoundary";
import { TitleBar } from "../ui/TittleBar";
import { getDataByBuckets, normalizeDataConfMatrix } from "../../utils/funcs";
import withContext from '../../HOC/withContext';

import "./index.scss";

@observer
class PredictorView extends Component {
  static propTypes = {
    predictorStore: PropTypes.object.isRequired,
    predictor: PropTypes.object.isRequired
  };

  static wrapNotes = notes =>
    Object.keys(notes).reduce((prev, type) => {
      if (notes[type] > 0) {
        prev.push({
          type,
          text: `${notes[type]} ${type}`
        });
      }
      return prev;
    }, []);

  static objectNotes = notes =>
    Object.keys(notes).reduce((prev, type) => {
      if (notes[type] > 0) {
        prev.push(
          <div className={`header-status ${type}`}>
            {notes[type]} {type}
          </div>
        );
      }
      return prev;
    }, []);

  static objectStatus = notes => (
    <div className="header-status info">{notes.text}</div>
  );

  state = {
    contentHeight: null,
    explantionData: {
      percentage: null,
      predictedField: "-",
      actual: "-",
      validColumn: null
    }
  };

  componentWillUnmount() {
    if (this.resizeObserver != null) {
      this.resizeObserver.disconnect();
    }
  }

  resizeObserver = null;

  contentBodyRef = null;

  onResize = () => {
    if (this.contentBodyRef) {
      const contentHeight = this.contentBodyRef.getBoundingClientRect().height;
      this.setState({ contentHeight });
    }
  };

  goToPredictors = e => {
    e.preventDefault();
    e.stopPropagation();
    navigation.setStage("predictors");
  };

  goToPredictorDataQuality = () => {
    navigation.setStage("predictorDataQuality");
  };

  setBodyResizeSubscribe = r => {
    this.contentBodyRef = r;
    if (this.resizeObserver != null) {
      this.resizeObserver.disconnect();
    }
    if (this.contentBodyRef != null) {
      this.resizeObserver = new ResizeObserver(this.onResize);
      this.resizeObserver.observe(this.contentBodyRef);
    }
  };

  hoverEvent = (data) => {
    this.setState({
      explantionData: {
        percentage: data.cell,
        predictedField: data.predictedField,
        actual: data.actual,
        validColumn: data.validColumn
      }
    });
  };

  enterAction = () => {
    console.log("hello in")
    this.props.context.sidebarContent.setAnyValue({
      title: { $set: "The Confusion Matrix" },
      paragraph: {
        $set:
          `A confusion matrix shows the performance an algorithm gets when solving a classification problem.
        Each entry in the confusion matrix can tell how many samples of each class were correctly classified
        and how many and where incorrectly classified.` },
      url: { $set: '' }
    })
  }

  leaveAction = () => {
    this.props.context.sidebarContent.setAnyValue({
      title: { $set: '' },
      paragraph: { $set: '' },
      url: { $set: '' }
    })
  }

  render() {
    const { predictorStore, predictor } = this.props;
    const { loading, data } = predictorStore;

    if (loading) {
      return <Loading />
    }
    const targetDataTypes = predictorStore.data["data_analysis"][
      "target_columns_metadata"
    ].reduce((prev, next) => {
      prev[next["column_name"]] = next["data_type"].toLowerCase();
      return prev;
    }, {});

    //#region training info
    const trainingInfoData = (data["model_analysis"] || []).map(model => ({
      name: model["column_name"],
      data: wrapHistXY(model["train_accuracy_over_time"])
    }));

    const accurate = `${predictor.predict} ${(predictorStore.data.accuracy * 100).toFixed(2)}% Accurate`

    return (
      <div className="main-content">
        <div className="header">
          <div />
          <TitleBar>
            Predictor Results for:{" "}
          </TitleBar>
          <span className="fields-predicted">
            {predictor.predict.length > 0
              ? predictor.predict.join(", ")
              : predictor.predict}
          </span>
          <Breadcrumbs
            goToDatasources={this.goToPredictors}
            dataSourceId={data.name}
            actualSite='Predictors'
          />
        </div>
        <div className="main-content__body" ref={this.setBodyResizeSubscribe}>
          <div className="predictor-container">
            <MindsPanel
              className="minds-block interactive-title"
              header={accurate}
              sectionTitleName={accurate}
              toggleable
              collapsed={false}
              style={{ margin: "0 0 20px 0" }}

            >
              <ErrorBoundary>
                <TrainingInfo
                  trainingData={trainingInfoData}
                  predictor={predictor}
                  dataPreparation={data["data_preparation"]}
                  accuracy={predictorStore.data.accuracy}
                  goToPredictorDataQuality={this.goToPredictorDataQuality}
                />
              </ErrorBoundary>
            </MindsPanel>
            <MindsPanel
              className="minds-block interactive-title"
              header="What is relevant for this model?"
              sectionTitleName="What is relevant for this model?"
              toggleable
              collapsed
              style={{ margin: "0 0 20px 0" }}
            >
              <ErrorBoundary>
                <ColumnImportance predictorStore={predictorStore} />
              </ErrorBoundary>
            </MindsPanel>
            {predictorStore.data.model_analysis.map(el => {
              const dataType = targetDataTypes[el["column_name"]];
              let dataResponse = getDataByBuckets("10", dataType, el["confusion_matrix"]);
              const dataKeys = dataResponse.map((item) => item.predictedField);
              dataResponse = normalizeDataConfMatrix(dataResponse, dataKeys);

              return (
                <MindsPanel
                  key={`confusion-matrix-${el["column_name"]}`}
                  className="minds-block interactive-title scroll-x confusion-matrix-block"
                  header={`When can you trust this model? - ${el["column_name"]}`}
                  sectionTitleName="When can you trust this model?"
                  toggleable
                  collapsed
                  style={{ marginBottom: "20px" }}
                >
                  <ErrorBoundary>
                    <div
                      onMouseEnter={() => this.enterAction()}
                      onMouseLeave={() => this.leaveAction()}
                      className="row">
                      <div className="column column__right">
                        <div className="trust-model-container">
                          <strong>Explanation:</strong>{" "}
                          <span className="label-gray">
                            Move the mouse over the graphic to get a detailed
                            explanation.
                        </span>
                          {this.state.explantionData.percentage &&
                            this.state.explantionData.percentage < 100 ? (
                              <p>
                                <span
                                  className={`tag tag__${
                                    this.state.explantionData.validColumn
                                      ? "success"
                                      : "error"
                                    }`}
                                >
                                  {this.state.explantionData.percentage + "%"}
                                </span>{" "}
                            of the times{" "}
                                <span className="tag tag__success">
                                  {this.state.explantionData.actual}
                                </span>{" "}
                                {
                                  this.state.explantionData.actual === this.state.explantionData.predictedField ?
                                    <>
                                      was correctly predicted by the model.
                                    </>
                                    :
                                    <>
                                      was supposed to be predicted by the model it miss
                                  classified it and confused it for{" "}
                                      <span className="tag tag__error">
                                        {this.state.explantionData.predictedField}
                                      </span>
                                    </>
                                }
                              </p>
                            ) : (
                              this.state.explantionData.percentage && (
                                <p>
                                  <span
                                    className={`tag tag__${
                                      this.state.explantionData.validColumn
                                        ? "success"
                                        : "error"
                                      }`}
                                  >
                                    {this.state.explantionData.percentage + "%"}
                                  </span>{" "}
                              of the time{" "}
                                  {this.state.explantionData.validColumn ? (
                                    <>
                                      <span className="tag tag__success">
                                        {this.state.explantionData.actual}
                                      </span>{" "}
                                  was correctly predicted by the model.
                                    </>
                                  ) : (
                                      <>
                                        <span className="tag tag__error">
                                          {this.state.explantionData.actual}
                                        </span>{" "}
                                  was supposed to be predicted by the model it
                                  miss classified it and confused it for{" "}
                                        <span className="tag tag__error">
                                          {this.state.explantionData.predictedField}
                                        </span>
                                      </>
                                    )}
                                </p>
                              )
                            )}
                          <br />
                        </div>
                      </div>
                      <div className="column column__left position-confusion">
                        <div style={{ height: dataResponse.length >= 10 ? 600 : 400 }}>
                          <ConfMatrix
                            hoverEvent={this.hoverEvent}
                            dataResponse={dataResponse}
                            dataKeys={dataKeys}
                          />
                        </div>
                      </div>
                    </div>
                  </ErrorBoundary>
                </MindsPanel>
              )
            })}

            {/* <MindsPanel
              className="minds-block interactive-title"
              header="Accuracy per Details"
              toggleable
              collapsed
              style={{ margin: 30 }}
            >
              <PredictorQuality predictorStore={ predictorStore } />
            </MindsPanel> */}
          </div>
          <div className="qtly-btn">
            <Button
              type="button"
              className="minds-action-btn"
              label="QUALITY"
              onClick={e => {
                e.preventDefault();
                e.stopPropagation();
                this.goToPredictorDataQuality();
                Mixpanel.track("Predictor Results > Quality");
              }}
            />
          </div>
          <div className="toolTip" />
        </div>
      </div>
    );
  }
}

export default withContext(PredictorView);
