import React from "react";
import { Checkbox } from "primereact/checkbox";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "../../inputText";
import { CalendarInput } from "../../ui/CalendarInput"

import "./index.scss";

class SimpleTable extends React.Component {
  state = {
    selectedValues: [],
    dataTable: [],
  };

  componentDidMount() {
    const { options, value } = this.props;

    this.setState({ dataTable: options, selectedValues: value || [] });
  }

  componentDidUpdate(_prevProps, _prevState) {
    const { options: newOptions, value: newValue } = this.props;
    const { options: oldOptions, value: oldValue } = _prevProps;

    if (newOptions.length !== oldOptions.length) {
      this.setState({ dataTable: newOptions });
    }

    if (newValue !== oldValue) {
      this.setState({ selectedValues: newValue || [] });
    }
  }

  onSearchBar = (e) => {
    const { options } = this.props;

    const searchText = e.target.value;
    const dataTable = options.filter((i) => i.label.indexOf(searchText) > -1);
    this.setState({ dataTable });
  };

  onSelectRow = (e, option) => {
    const { onChange = () => {}, options, useDeleteIcon } = this.props;
    let { selectedValues } = this.state;

    let disableCheck = false;
    if (!options || options.length <= 1) {
      disableCheck = true;
    }
    if (useDeleteIcon && options.length - selectedValues.length <= 1) {
      disableCheck = true;
    }
    if (disableCheck) {
      console.error(
        "You need to leave at least one item without being selected"
      );
    }

    if (e.checked && !disableCheck) {
      selectedValues.push(e.value);
    } else {
      selectedValues = selectedValues.filter((i) => i !== e.value);
    }

    e.target.value = selectedValues;
    onChange(e, option);

    this.setState({ selectedValues });
  };

  validateChecked = (checkedValue) => {
    const { selectedValues } = this.state;

    return selectedValues.indexOf(checkedValue) > -1;
  };

  setExtraFieldValue = (e, option) => {
    const { onExtraFieldChange } = this.props;
    option.extraFieldValue.value = e.target.value;
    onExtraFieldChange(option.extraFieldValue);
  };

  setDateExtraFieldValue = (e, option) => {
    const { onExtraFieldChange } = this.props;
    option.extraFieldValue.value = e;
    onExtraFieldChange(option.extraFieldValue);
  };

  render() {
    const {
      name,
      filter,
      searchIconStyles,
      useDeleteIcon,
      showExtraFields,
    } = this.props;

    const { dataTable } = this.state;

    const getOptions = (option) => {
      if (!option.distribution) {
        return [];
      }
      const options = option.distribution
        .sort()
        .map((el) => ({ label: el, value: el }));
      options.unshift({ label: "none", value: null });
      return options;
    };

    return (
      <div className="minds-dialog__field" style={{ width: "100%", margin: 0 }}>
        {filter ? (
          <div className="p-inputgroup" style={{ width: "100%" }}>
            <InputText
              name="predictorSearch"
              onChange={this.onSearchBar}
              className="mdb-input search-bar"
              placeholder="Search Bar"
            />
            <span
              className="p-inputgroup-addon search-icon"
              style={searchIconStyles}
            >
              <i className="pi pi-search"></i>
            </span>
          </div>
        ) : null}

        <div className="simple-table-container">
          {dataTable.map((option, idx) => (
            <div className="simple-row-flex" key={idx}>
              <div className="simple-column-flex">
                <div className="column-content">
                  {option.label ? option.label : option.name}
                </div>
              </div>
              <div className="simple-column-flex">
                <div className="column-content">
                  {!showExtraFields ? (
                    <Checkbox
                      name={name}
                      value={option.value}
                      onChange={(e) => this.onSelectRow(e, option)}
                      checked={this.validateChecked(option.value)}
                      className={`mdb-select ${
                        useDeleteIcon ? "use-delete-icon" : ""
                      }`}
                    />
                  ) : null}
                  {showExtraFields ? (
                    <div style={{ marginLeft: "20px" }}>
                      {option.data_type === "numeric" ? (
                        <input
                          name="value"
                          placeholder="value"
                          className="query-condition__value mdb-input"
                          value={option.extraFieldValue.value || ""}
                          onChange={(e) => this.setExtraFieldValue(e, option)}
                          style={{ width: 150 }}
                        />
                      ) : option.data_type === "date" ? (<>
                        <CalendarInput
                          value={null}
                          onChangeDate={(e) => this.setDateExtraFieldValue(e, option)}
                        />
                      </>)
                      :(
                        <Dropdown
                          className="query-condition__value mdb-select"
                          name="value"
                          placeholder="value"
                          value={option.extraFieldValue.value}
                          options={getOptions(option)}
                          onChange={(e) => this.setExtraFieldValue(e, option)}
                          style={{ width: 150 }}
                        />
                      )}
                    </div>
                  ) : null}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default SimpleTable;
