import { observable, action, runInAction, decorate } from 'mobx';
import MindsDB from 'mindsdb-js-sdk';

class ConnectionStore {
  connected = false;
  checkIntervalId = null;
  url = 'http://localhost:47334';

  async connect(url, params) {
    if (this.connected === true) {
      this.disconnect();
    }

    this.params = params;
    MindsDB.connect(url, this.params);
    let connectionCheck = false;

    try {
      connectionCheck = await MindsDB.ping(this.params);
    } catch (e) {
      connectionCheck = false;
    }

    // if (connectionCheck === true) {
      runInAction(() => {
        this.connected = true;
        this.url = url;
        this.checkIntervalId = setInterval(() => {
          this.checkConnection(this.params);
        }, 10000);
        // NOTE can be problems if ping query will longer than timeout
      });
    // } else {
    //   window.growl.show({ severity: 'error', summary: 'error', detail: 'Connection failed' });
    //   this.disconnect();
    // }
  }

  disconnect() {
    if (this.checkIntervalId != null) clearInterval(this.checkIntervalId);
    MindsDB.disconnect();
    this.connected = false;
  }

  async checkConnection(params) {
    let connectionCheck = false;
    try {
      connectionCheck = await MindsDB.ping(params);
    } catch (e) {
      connectionCheck = false;
    }
    if (connectionCheck !== true) this.disconnect();
  }
}

decorate(ConnectionStore, {
  connected: observable,
  url: observable,
  connect: action,
  disconnect: action,
  checkConnection: action,
});

export default new ConnectionStore();
