import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styled from '@emotion/styled';

import InterfaceView from './interfaceView';
import { generateDataSet } from './graphOptions';
import { Colors } from '../../ui/Colors';
import NivoBar from '../../ui/Bar';
import NivoTreeMap from '../../ui/TreeMap';
import { TargetColumnTitle } from './styles';

const ArrowsContainer = styled.div`
  width: 160px;
  margin: 0 auto;
  display: flex;
  justify-content: center;

  button {
    cursor: pointer;
    border: none;
    background: transparent;
    font-size: 12px;
    font-weight: 700;
    outline: 0;
    margin: 0 15px;
  }
`;

const MAX_FIELDS = 50;

const DataQualityTargetColumn = ({ data }) => {

  const [dataIndex, setDataIndex] = useState(0);
  const [actual, setActual] = useState(MAX_FIELDS);

  const { data_analysis_v2, column_name, warnings } = data;
  const reponseData = generateDataSet(
    data['data_distribution']['data_histogram'],
    data_analysis_v2.typing.data_type,
    data_analysis_v2.outliers ? data_analysis_v2.outliers.outlier_buckets : []
  );
  const { labels, formatData } = reponseData;

  const total = formatData.length;
  const splicedFormatData = [];
  const splicedLabels = [];
  if (formatData.length > MAX_FIELDS) {
    while (formatData.length > 0) {
      splicedFormatData.push(formatData.splice(0, MAX_FIELDS));
      splicedLabels.push(labels.splice(0, MAX_FIELDS));
    }
  } else {
    splicedFormatData.push(formatData);
    splicedLabels.push(labels);
  }

  const hasMultiple = splicedFormatData.length > 1;
  const decreaseIndex = () => {
    if (dataIndex - 1 < 0) {
      setDataIndex(0);
      setActual(MAX_FIELDS);
    } else {
      setDataIndex(dataIndex - 1);
      setActual(actual - splicedFormatData[dataIndex].length);
    }
  };

  const increaseIndex = () => {
    if (dataIndex + 1 < splicedFormatData.length) {
      setDataIndex(dataIndex + 1);
      setActual(actual + splicedFormatData[dataIndex + 1].length);
    } else {
      setDataIndex(dataIndex);
      setActual(total);
    }
  };

  const getColor = (bar) => bar.data.color || Colors.green;

  const transformTextData = () => {
    const textData = Object.keys(data_analysis_v2.word_dist).map((word) => ({
      name: !word ? 'Unknown' : word,
      loc: data_analysis_v2.word_dist[word],
    }));

    return {
      children: [...textData],
    };
  };

  return (
    <div className="row-grid">
      <div className="col-sm-12 col-md-4">
        <InterfaceView
          warnings={warnings}
          data={data_analysis_v2}
          column_name={column_name}
        />
      </div>
      <div className="col-sm-12 col-md-8">
        {data_analysis_v2?.typing?.data_type !== 'Text' && (
          <>
            <TargetColumnTitle>
              {' '}
              Occurrences of Variable on the Dataset{' '}
            </TargetColumnTitle>
            <div style={{ paddingRight: 20, height: 400 }}>
              <NivoBar
                typeValues={data_analysis_v2.typing.data_type}
                data={
                  hasMultiple
                    ? splicedFormatData[dataIndex]
                    : splicedFormatData[0]
                }
                indexBy="key"
                keys={hasMultiple ? splicedLabels[dataIndex] : splicedLabels[0]}
                color={getColor}
                tooltipTitle="Occurrences"
                yLabelSymbol="%"
                animate
              />
              {hasMultiple && (
                <ArrowsContainer>
                  <button onClick={decreaseIndex}>{'<<'}</button>
                  <span>{`${actual}/${total}`}</span>
                  <button onClick={increaseIndex}>{'>>'}</button>
                </ArrowsContainer>
              )}
            </div>
          </>
        )}

        {data_analysis_v2.typing.data_type === 'Text' && (
          <div style={{ paddingRight: 20, height: 400 }}>
            <NivoTreeMap root={transformTextData()} theme="greens" />
          </div>
        )}
      </div>
    </div>
  );
};

DataQualityTargetColumn.propTypes = {
  data: PropTypes.object.isRequired,
  score: PropTypes.number,
  data_analysis_v2: PropTypes.object,
};

DataQualityTargetColumn.defaultProps = {
  data_analysis_v2: {},
  score: 0,
};

export default DataQualityTargetColumn;
