import React from 'react';
import { ResponsiveBar } from '@nivo/bar';
import styled from '@emotion/styled';

import { Colors } from "../../ui/Colors";

const ToolTip = styled.div`
  color: ${Colors.slateGrey};
  cursor: pointer;

  strong {
    color: ${Colors.mirage};
  }
`

const NivoBar = ({ tooltipTitle, animate, typeValues, data, indexBy, keys, fillType, color, yLabelSymbol }) => {

  return (
    <ResponsiveBar
      data={data}
      indexBy={indexBy}
      keys={keys}
      padding={0.4}
      defs={[
        {
          id: 'dots',
          type: 'patternDots',
          background: 'inherit',
          color: '#38bcb2',
          size: 4,
          padding: 1,
          stagger: true
        },
        {
          id: 'lines',
          type: 'patternLines',
          background: 'inherit',
          color: '#eed312',
          rotation: -45,
          lineWidth: 6,
          spacing: 10
        }
      ]}
      fill={[{ match: '*', id: fillType ? fillType : '' }]}
      colors={color}
      margin={{
        top: 20,
        right: 40,
        bottom: 100,
        left: 70
      }}
      labelSkipWidth={12}
      labelSkipHeight={12}
      tooltip={({ color, data }) => (
        <ToolTip color={color}>
          <strong>{tooltipTitle}: </strong> {data.tooltip}
        </ToolTip>
      )}
      enableGridX={true}
      axisTop={null}
      axisRight={null}
      axisBottom={{
        tickSize: 5,
        tickPadding: 10,
        tickRotation: -45,
        legend: '',
        legendPosition: 'middle',
        legendOffset: 32,
        tickValues: typeValues === 'Numeric' && keys.length > 5 ? keys.map((item) => item).filter((item,index,arr)=>(arr.splice(index,1))) : ''
      }}
      axisLeft={{
        tickSize: 10,
        tickPadding: 10,
        tickRotation: 0,
        legendPosition: 'middle',
        legendOffset: -40,
        format: v => yLabelSymbol ? `${v}${yLabelSymbol}` : v,
      }}
      labelTextColor={{ from: 'color', modifiers: [['darker', 1.6]] }}
      enableLabel={false}
      animate={animate}
      motionStiffness={90}
      motionDamping={15}
    />
  )
}

export default NivoBar;
