import React from 'react';
import { Breadcrumb, BreadcrumbItem } from './styles';

export const Breadcrumbs = ({
	goToDatasources,
	dataSourceId,
	actualSite,
	AditionalItemGoto,
	AditionalItemName = ''
}) => (
	<Breadcrumb>
		<BreadcrumbItem>
			<a href="/" className="breadcrumb-link" role="button" onClick={goToDatasources}>
				{actualSite}
			</a>
		</BreadcrumbItem>
		{AditionalItemName && (
			<BreadcrumbItem>
				<a href="/" className="breadcrumb-link" role="button" onClick={AditionalItemGoto}>
					{AditionalItemName}
				</a>
			</BreadcrumbItem>
		)}
		<BreadcrumbItem>{dataSourceId}</BreadcrumbItem>
	</Breadcrumb>
);
