import React, { Component } from "react";
import { observer } from "mobx-react";
import MindsPanel from "../common/mindsPanel";
import WarningMark from "../ui/warningMark";
import DataQualityTargetColumn from "./dataQualityTargetColumn";
import predictorStore from "../../store/predictor";
import navigation from "../../store/navigation";
import { Breadcrumbs } from '../ui/Breadcrumbs';
import { TitleBar } from '../ui/TittleBar';

import { qualityWarnings } from '../../utils/funcs';
import "./index.scss";

@observer
class DataQuality extends Component {
  static goToPredictors(e) {
    e.preventDefault();
    e.stopPropagation();

    navigation.setStage("predictors");
  }

  static goToPredictor(e) {
    e.preventDefault();
    e.stopPropagation();

    navigation.setStage("predictorView");
  }

  render() {
    const targetColumnsMetadata = predictorStore.data|| [];
    const inputColumnsMetadata =
      predictorStore.data["data_analysis"]["input_columns_metadata"];

    const dataQualityTargetColumns = targetColumnsMetadata["data_analysis"]["target_columns_metadata"].map((columnMetadata) => {

      columnMetadata.data_analysis_v2 = targetColumnsMetadata["data_analysis_v2"][columnMetadata['column_name']];
      columnMetadata.warnings = qualityWarnings(columnMetadata.data_analysis_v2);
      columnMetadata.warningsLength = Object.keys(columnMetadata.warnings).filter(x => columnMetadata.warnings[x]).length;

        return (
          <MindsPanel
            className="minds-block interactive-title"
            key={ `key-${columnMetadata['column_name']}` }
            header={
              <div className="collapsed-panelTitle">
                <span>
                  {columnMetadata["column_name"]}{" "}
                </span>
                {
                  columnMetadata.warningsLength > 0 && (
                    <div className="warn-collapsed">
                      <span className="low-span">{columnMetadata.warningsLength} Warnings&nbsp;</span>
                      <WarningMark type='yellow' />
                    </div>
                  )
                }
              </div>
            }
            sectionName={columnMetadata["column_name"]}
            columnMetadata={columnMetadata}
            noRepeat
            toggleable
            collapsed
          >
            <DataQualityTargetColumn data={columnMetadata} />
          </MindsPanel>
        );
      }
    );

    const dataQualityInputColumns = inputColumnsMetadata.map((columnMetadata) => {
      const { predictorDataQualityCol } = navigation;

      columnMetadata.data_analysis_v2 = targetColumnsMetadata["data_analysis_v2"][columnMetadata['column_name']];
      columnMetadata.warnings = qualityWarnings(columnMetadata.data_analysis_v2);
      columnMetadata.warningsLength = Object.keys(columnMetadata.warnings).filter(x => columnMetadata.warnings[x]).length;

      return (
        <MindsPanel
          className="xyz minds-block interactive-title quality"
          key={ `key-${columnMetadata['column_name']}` }
          header={
            <>
              <span className="width-column-1">
                {columnMetadata["column_name"]}
              </span>
              {
                columnMetadata.warningsLength > 0 &&
                  <div className="width-column-2">
                  <span className="low-span">{columnMetadata.warningsLength} Warnings&nbsp;</span>
                  <WarningMark type='yellow' />
                </div>
              }
            </>
          }
          sectionName={columnMetadata["column_name"]}
          columnMetadata={columnMetadata}
          collapsed={predictorDataQualityCol === columnMetadata["column_name"] ? false : true}
          noRepeat
          toggleable
        >
          <DataQualityTargetColumn data={columnMetadata} />
        </MindsPanel>
      );
    });

    return (
      <div className="main-content">
        <div className="header">
          <div />
          <TitleBar>Predictor Data Quality</TitleBar>
          <Breadcrumbs
            goToDatasources={this.goToPredictors}
            dataSourceId='Data Quality'
            actualSite='Predictors'
            AditionalItemGoto={this.constructor.goToPredictors}
            AditionalItemName={predictorStore.data.name}
          />

        </div>
        <div className="main-content__body data-quality">
          <div className="data-container">
            {dataQualityTargetColumns}
            {dataQualityInputColumns}
          </div>
        </div>
      </div>
    );
  }
}

export default DataQuality;
