import React from 'react';
import { Panel } from 'primereact/panel';

import WarningMark from '../../ui/warningMark';
import DataQualityTargetColumn from '../../predictorDataQuality/dataQualityTargetColumn'
import { TabContainer, SpanTab } from './styles'
/**
 * panel with collaps/expand by header click (not just icon)
 */
class MindsPanel extends Panel {
  renderHeader(collapsed) {
    const { header, sectionName, toggleable, columnMetadata, noRepeat } = this.props;

    if (header || toggleable) {
      const toggleIcon = this.renderToggleIcon(collapsed);
      return (
        <>
          <div
            className="p-panel-titlebar"
            role="button"
            onClick={this.toggle}
          >
            { collapsed ?
                <div className="p-panel-title">{header}</div>
              :
              sectionName ? (
                <TabContainer>
                  <strong>{sectionName}</strong>
                  {
                    columnMetadata.warningsLength > 0 && (
                      <SpanTab>
                        <span>{columnMetadata.warningsLength} warnings</span>
                        <WarningMark
                          className="width-column-2"
                          type="yellow"
                        />
                      </SpanTab>
                    )
                  }
                </TabContainer>
                ) :  <strong>{header}</strong>
            }
            {toggleIcon}
          </div>
          {!collapsed && columnMetadata && !noRepeat &&
            <DataQualityTargetColumn data={ columnMetadata } />
          }
        </>
      );
    }
    return null;
  }
}

export default MindsPanel;
