import React from 'react';
import { ResponsivePie } from '@nivo/pie';
import { PieContainer, TextContainer, Info, InfoConfidence } from './styles';

const Pie = ({
	confidenceInterval,
	height,
	data,
	infoData = '',
	infoDatatext2 = '',
	endAngle = 360,
	mtop = 0,
	mleft = 0,
	pieColor
}) => (
	<PieContainer style={{ height: height }}>
		<TextContainer style={{ top: `${mtop}px`, left: `${mleft}px` }}>
			<InfoConfidence>{confidenceInterval}</InfoConfidence>
			<Info style={{ fontSize: infoData === 'Test' ? '20px' : '17px' }}>{infoData}</Info>
			<Info>{infoDatatext2}</Info>
		</TextContainer>

		<ResponsivePie
			data={data}
			margin={{ top: 10, right: 0, bottom: 10, left: 0 }}
			innerRadius={0.75}
			padAngle={0.7}
			cornerRadius={0}
			enableSlicesLabels={false}
			colors={[ pieColor ]}
			borderWidth={3}
			borderColor={{ from: 'color', modifiers: [ [ 'darker', 0.2 ] ] }}
			radialLabelsSkipAngle={10}
			fit={false}
			radialLabelsTextXOffset={6}
			radialLabelsTextColor={pieColor}
			radialLabelsLinkOffset={-24}
			radialLabelsLinkDiagonalLength={0}
			endAngle={endAngle}
			enableRadialLabels={false}
			slicesLabelsSkipAngle={10}
			slicesLabelsTextColor={pieColor}
			isInteractive={false}
			animate={true}
			motionStiffness={90}
			motionDamping={15}
			defs={[]}
			fill={[]}
		/>
	</PieContainer>
);

export default Pie;
