
import { orderBy } from 'lodash'

import { Colors } from "../../ui/Colors"
import { SFMono } from "../../ui/Fonts"
import { wrapHistXY } from "../../../utils/funcs";
import { filterAxisData } from "../../../utils/funcs";
import { flagsDictionary } from "../../../utils/flagsDictionary";

export const barOptions = {
  easing: "easeOutQuart",
  legend: false,
  scales: {
    yAxes: [
      {
        ticks: {
          fontFamily: SFMono,
          fontColor: Colors.mirage,
          min: 0,
          suggestedMin: 0,
          beginAtZero: true
        }
      }
    ],
    xAxes: [
      {
        ticks: {
          fontFamily: SFMono,
          fontColor: Colors.mirage,
          min: 0,
          suggestedMin: 0,
          beginAtZero: true,
          scaleBeginAtZero: true
        }
      }
    ]
  }
}

export const generateDataSet = (data, dataType, outliers) => {
  const chartData = wrapHistXY(data);
  const labels = filterAxisData(chartData, "x");
  const labelsGrouped = []
  let innerGroup = []
  const isNumerical = dataType && dataType.toLowerCase() === 'numeric'
  const isCategorical = dataType && dataType.toLowerCase() === 'categorical'

  if (isNumerical) {
    for (let i = 0; i < labels.length; i++) {
      innerGroup.push(labels[i])

      if (innerGroup.length > 1) {
        labelsGrouped.push(innerGroup.join('-'))
        innerGroup = []
        innerGroup.push(labels[i])
      }
    }
  }

  let total = 0
  chartData.forEach(data => {
    total += data.y && !isNaN(data.y) ? Math.abs(data.y) : 0
  })

  const fullLength = chartData.length
  let newLastOutlierItem = {}
  let formatData = chartData.map((data, idx) => {
    let dataResponse = {};

    const value = data.y && !isNaN(data.y) ? Math.abs(data.y) : 0
    const percentage = value * 100 / total
    const fixedPercentage = !Number.isInteger(percentage) ? parseFloat(percentage.toFixed(2)) : percentage

    if (labels.includes(data.x)) {
      const isOutlier = outliers.find(outlier => {
        const item = !Number.isInteger(outlier) ? parseFloat(outlier.toFixed(2)) : outlier
        return item === data.x
      })

      if (isNumerical) {
        const actualLabel = labelsGrouped.find(item => item.indexOf(`${data.x}-`) > -1)
        if (actualLabel) {
          dataResponse = {
            key: actualLabel,
            [actualLabel]: fixedPercentage,
            sortingKey: fixedPercentage,
            tooltip: `Values between [${actualLabel}) are ${fixedPercentage}% common`,
          }
        } else if ((idx + 1) === fullLength) {
          const lastOutlierIncluded = labelsGrouped.find(item => item.indexOf(`-${data.x}`) > -1)
          if (lastOutlierIncluded) {
            newLastOutlierItem = {
              key: `${data.x}`,
              [data.x]: fixedPercentage,
              sortingKey: fixedPercentage,
              tooltip: `Values in [${data.x}] are ${fixedPercentage}% common`,
            }
          }
        }
      } else {
        dataResponse = {
          key: data.x,
          [data.x]: fixedPercentage,
          sortingKey: fixedPercentage,
          tooltip: `Values in [${data.x}] are ${fixedPercentage}% common`,
        }
      }

      if (!isNaN(isOutlier)) {
        dataResponse.color = Colors.watermelon
        dataResponse.outlier = data.x

        if ((idx + 1) === fullLength) {
          newLastOutlierItem = {
            ...newLastOutlierItem,
            color: Colors.watermelon,
            outlier: data.x
          }
        }
      } else {
        newLastOutlierItem = {}
      }
    }

    return dataResponse;
  }).filter((data) => data.key === 0 ? true : data.key);

  if (isCategorical) {
    formatData = orderBy(formatData, ['sortingKey'], ['desc'])
  }

  if (newLastOutlierItem && newLastOutlierItem.key) {
    formatData.push(newLastOutlierItem)

    if (isNumerical) {
      labelsGrouped.push(`${newLastOutlierItem.key}`)
    } else {
      labels.push(`${newLastOutlierItem.key}`)
    }
  }

  return {
    labels: isNumerical ? labelsGrouped : labels,
    chartData: chartData,
    formatData: formatData,
    datasets: [
      {
        label: "Occurrences",
        backgroundColor: "rgba(0, 176, 109, 0.6)",
        borderColor: "rgba(0, 176, 109, 0.6)",
        hoverBackgroundColor: "rgb(0, 176, 109)",
        hoverBorderColor: "rgb(0, 176, 109)",
        data: filterAxisData(chartData, "y")
      }
    ],
  };
}

export const getLanguageFlags = (lang) => flagsDictionary[lang]
