import React from 'react';
import WarningMark from '../warningMark';

const Loading = ({ message, type, iconMessage }) => (
  <>
    <div className="loader-bars">
      <div className="bars">
        <div className="rect1" />
        <div className="rect2" />
        <div className="rect3" />
        <div className="rect4" />
        <div className="rect5" />
      </div>
      {
        message && (
          <div className="message-loader">
            <WarningMark type={type} icon={iconMessage} />
            {message}
          </div>
        )
      }
    </div>
  </>
);

export default Loading;
