import { action, observable, runInAction, decorate } from 'mobx';

class DataSourceQualityStore {
  dataSourceName = null;
  data = null;
  dataSource = null;
  loading = false;
  loaded = false;
  analyzeStatus = 'analyzing';

  async loadDataQuality(dataSource, params) {
    this.dataSourceName = dataSource.name;
    this.dataSource = dataSource;
    this.loading = true;
    this.analyzeStatus = 'analyzing';

    try {
      const dataQuality = await dataSource.loadDataQuality(params);
      if (dataQuality.status === 'analyzing') {
        return new Promise((resolve) => {
          setTimeout(() => {
            resolve(this.loadDataQuality(dataSource, params));
          }, 5000);
        });
      }

      runInAction(() => {
        this.data = dataQuality;
        this.loading = this.data.status !== "analyzing" ? false : true;
        this.loaded = true;
        this.analyzeStatus = this.data.status;
      });
    } catch (e) {
      // window.showError(e);
      runInAction(() => this.loading = false);
    }
  }
}

decorate(DataSourceQualityStore, {
  dataSourceName: observable,
  data: observable,
  dataSource: observable,
  loading: observable,
  loaded: observable,
  loadDataQuality: action,
});

export default new DataSourceQualityStore();
