import React from 'react';
import PropTypes from 'prop-types';
import { HeaderTittle } from './styles'

const TableBottomSelector = ({ options, onSelect }) => {
	return (
		<HeaderTittle>
			{options.map((option, index) => (
				<button
					className={option.selected ? 'option selected' : 'option'}
					key={`key-${index}`}
					type="button"
					onClick={() => {
						onSelect(option);
					}}
				>
					{`${option.name}`.toUpperCase()}
				</button>
			))}
		</HeaderTittle>
	);
};

TableBottomSelector.propTypes = {
	options: PropTypes.array.isRequired,
	onSelect: PropTypes.func.isRequired
};

export default TableBottomSelector;
