import update from 'immutability-helper';

const sidebarContent = (context) => ({
  title: '',
  paragraph: '',
  url: '',
  setAnyValue: (states, callback) => {
    context.setState({
      sidebarContent: update(context.state.sidebarContent, states),
    }, () => {
      if (callback) {
        callback()
      }
    })
  },
})

export default sidebarContent
