import React from "react";
import PropTypes from "prop-types";
import { getLanguageFlags } from "./graphOptions";
import WarningMark from '../../ui/warningMark';
import { InterfaceContainer, ItemRow, Content, OutliersValues, TextContainer } from './styles';
import withContext from '../../../HOC/withContext';

const InterfaceView = (props) => {
  const { warnings, data, column_name, context } = props;
  const { typing, empty, unique, outliers, bias, avg_words_per_sentence, lang_dist } = data;

  const enterAction = (content) => {
    context.sidebarContent.setAnyValue({
      title: { $set: content.title },
      paragraph: { $set: content.description },
      url: { $set: content.url }
    })
  }

  const leaveAction = () => {
    context.sidebarContent.setAnyValue({
      title: { $set: '' },
      paragraph: { $set: '' },
      url: { $set: '' }
    })
  }

  const getOutliers = (outliers) => {
    if (outliers) {
      return outliers
        .sort((a, b) => a - b)
        .map(item => !Number.isInteger(item) ? item.toFixed(2) : item)
        .join(', ')
    }

    return ''
  }

  return (
    <InterfaceContainer>
      {data && (
        <>
          <ItemRow
            onMouseEnter={() => enterAction({ title: 'Type:', description: typing.description })}
            onMouseLeave={() => leaveAction()}
          >
            <strong>Type:</strong> {`${typing.data_type}/${typing.data_subtype}`}
            {/* <CogIcon className="fas fa-cog" /> */}
          </ItemRow>

          {typing.data_type !== 'Text' && (
            <>
              <ItemRow
                onMouseEnter={() => enterAction({ title: 'Missing Values:', description: empty.description })}
                onMouseLeave={() => leaveAction()}
              >
                <strong>Missing Values:</strong> {`${empty.empty_cells}/${empty.empty_percentage.toFixed(2)}%`}
                {warnings.empty && (
                  <WarningMark className="width-column-2" type="yellow" />
                )}
              </ItemRow>

              {unique && (
                <ItemRow
                  onMouseEnter={() => enterAction({ title: 'Distinct Values:', description: unique.description })}
                  onMouseLeave={() => leaveAction()}
                >
                  <strong>Distinct Values:</strong> {unique.unique_values}
                  {warnings.unique && (
                    <WarningMark className="width-column-2" type="yellow" />
                  )}
                </ItemRow>
              )}

              {(outliers && outliers.outlier_buckets.length > 0) && (
                <ItemRow
                  onMouseEnter={() => enterAction({ title: 'Potential Outliers:', description: outliers.description })}
                  onMouseLeave={() => leaveAction()}
                >
                  <strong>Potential Outliers:</strong>
                  <WarningMark className="width-column-2" type="yellow" />
                  <Content>
                    Here are some potential outliers in {' '}
                    <span className="tag tag__success">{column_name}</span> for values in:{' '}
                    <OutliersValues>
                      <span className="tag tag__error">
                        {getOutliers(outliers.outlier_buckets)}
                      </span>
                    </OutliersValues>
                  </Content>
                </ItemRow>
              )}

              {(bias && warnings.bias) && (
                <ItemRow
                  onMouseEnter={() => enterAction({ title: 'Potential Bias:', description: bias.description })}
                  onMouseLeave={() => leaveAction()}
                >
                  <strong>Potential Bias:</strong>
                  {warnings.bias && (
                    <WarningMark className="width-column-2" type="yellow" />
                  )}
                  <Content>
                    {bias.warning}
                  </Content>
                </ItemRow>

              )}
            </>
          )}

          {typing.data_type === 'Text' && (
            <>
              <ItemRow>
                <strong>Average Words per Sentece:</strong> {avg_words_per_sentence.toFixed(2)}
              </ItemRow>
              {
                lang_dist && (
                  <ItemRow>
                    <strong>Languages:</strong>
                    <TextContainer>
                      {Object.keys(lang_dist).map((lang, idx) => (
                        <div key={idx}>
                          {`${getLanguageFlags(lang) ?
                              getLanguageFlags(lang).flag
                              : getLanguageFlags("default").flag}(${lang}): ${lang_dist[lang]}`
                          }
                        </div>
                      ))}
                    </TextContainer>
                  </ItemRow>
                )
              }
            </>
          )}
        </>
      )}
    </InterfaceContainer>
  )
}

InterfaceView.propTypes = {
  column_name: PropTypes.string.isRequired,
  data: PropTypes.object
};

InterfaceView.defaultProps = {
  data: {}
};

export default withContext(InterfaceView);
