import {
  observable, action, computed, runInAction, decorate
} from 'mobx';
import MindsDB from 'mindsdb-js-sdk';

class DatasourcesStore {
  data = [];
  loading = false;
  loaded = false;

  get dropdownOptions() {
    return this.data.map(
      (el, index) => ({ label: el.name, value: index })
    );
  }

  getFields = (index) => this.data[index].columns || [];

  getByName = (name) => {
    for (let i = 0; i < this.data.length; i++) {
      if (this.data[i].name === name) return this.data[i];
    }
    return null;
  };

  reset = () => {
    this.loading = false;
    this.loaded = false;
    this.data = [];
  };

  async load(params) {
    this.loading = true;
    try {
      const dataSources = await MindsDB.dataSources(params);
      runInAction(() => {
        this.data = dataSources;
        this.loading = false;
        this.loaded = true;
      });
    } catch (e) {
      // window.showError(e);
      runInAction(() => this.loading = false);
    }
  }

  async download(opts, params) {
    try {
      await MindsDB.DataSource(opts).download(params);
    } catch (e) {
      // window.showError(e);
    }
  }

  async delete(opts, params) {
    try {
      await MindsDB.DataSource(opts).delete(params);
      await this.load(params);
      // window.growl.show({ severity: 'success', detail: 'Data source removed' });
    } catch (e) {
      // window.showError(e);
    }
  }
}

decorate(DatasourcesStore, {
  data: observable,
  loading: observable,
  loaded: observable,
  dropdownOptions: computed,
  reset: action,
  load: action,
  download: action,
  delete: action
});

export default new DatasourcesStore();
