import React from "react";
import PropTypes from "prop-types";
import { ButtonComponent } from "../ui/Button";
import  SidebarContent from "../ui/SidebarContent";
import { MenuItem } from "../menuItems";
import { SeparationDiv, HelpButton , Img} from "./styles"
import mindsdblogo from '../../assets/mindsdb-logo.svg'


import "./index.scss";
// const { shell } = require("electron");

const MindsMenu = ({ items, onMenuClick, connectionStore, classUl }) => {
  const openJira = (e) => {
    // shell.openExternal(
    //   "https://mindsdb.atlassian.net/servicedesk/customer/portals"
    // );
  };

  const { connected } = connectionStore;

  return (
    <div className="minds-menu-holder">
      <div className="logo-holder">
        <Img
          src={mindsdblogo}
          alt="logo"
        />
      </div>
      <ul className={`${classUl} minds-menu`}>
        <MenuItem
          items={items}
          onMenuClick={onMenuClick}
          connected={connected}
        />
      </ul>
      <SeparationDiv />

      <SidebarContent />

      <ButtonComponent
        stylesType="help"
        callback={openJira}
      >
        {HelpButton}
      </ButtonComponent>
    </div>
  );
};

MindsMenu.propTypes = {
  connectionStore: PropTypes.object.isRequired,
  items: PropTypes.array.isRequired,
  onMenuClick: PropTypes.func.isRequired,
  className: PropTypes.string
}

export default MindsMenu;
