import React, { Component } from "react";
import PropTypes from "prop-types";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import { observer } from "mobx-react";
import DataTable from '../ui/mindsDataTable';
import Moment from "moment";

import EmptyPage from "../common/emptyPage";
import NewPredictorWin from "./newPredictorWin";
import dataSourcesStore from "../../store/dataSources";
import UploadWin from "./uploadWin";
import navigation from "../../store/navigation";
import { Mixpanel } from "../../utils/mixpanel";
import { TitleBar } from '../ui/TittleBar';
import DarkMode from "../ui/darkMode";

import "./index.scss";

@observer
class Predictors extends Component {
  static propTypes = {
    predictorsStore: PropTypes.object.isRequired
  };

  state = {
    uploadWinVisible: false,
    newPredictorWinVisible: false,

    thWidths: null,
    thHeight: null
  };

  componentWillMount() {
    this.reload();
  }

  componentDidMount() {
    this.resizeObserver = new ResizeObserver(this.onResize);
    this.mutationObserver = new MutationObserver(this.onResize);
    this.resizeObserver.observe(this.wrapperRef);
    this.mutationObserver.observe(this.wrapperRef, {
      childList: true,
      subtree: true
    });
  }

  componentWillUnmount() {
    this.resizeObserver.disconnect();
    this.mutationObserver.disconnect();
    if (this.interval) {
      clearInterval(this.interval);
    }
  }

  onResize = () => {
    // get cells widths
    const row = this.wrapperRef.querySelector(".p-datatable-row");
    let thWidths = null;
    let thHeight = null;
    if (row) {
      thWidths = new Array(row.children.length);
      for (let i = 0; i < row.children.length; i++) {
        thWidths[i] = row.children[i].getClientRects()[0].width;
      }
    }
    // get title height
    const header = this.wrapperRef.querySelector(".p-datatable-thead > tr");
    if (header) {
      thHeight = header.getBoundingClientRect().height;
    }
    this.setState({
      thWidths,
      thHeight
    });
  };

  interval = null;

  reload = () => {
    const { predictorsStore } = this.props;
    predictorsStore.load();

    if (this.interval) {
      clearInterval(this.interval);
      this.interval = null;
    }
    this.interval = setInterval(() => {
      if (predictorsStore.loading === false) {
        predictorsStore.load();
      }
    }, 5000);
  };

  onDownload = async predictor => {
    try {
      await predictor.download();
    } catch (e) {
      // window.showError(e);
    }
  };

  onUpload = () => { };

  onDelete = async predictor => {
    try {
      await predictor.delete();
      this.reload();
      // window.growl.show({ severity: "success", detail: "Predictor removed" });
    } catch (e) {
      // window.showError(e);
    }
  };

  onSelectionChange = e => {
    if (e.value.status === "training") return;

    navigation.openPredictor(e.value);
  };

  hideNewPredictorWin = needReload => {
    this.setState({
      newPredictorWinVisible: false
    });
    if (needReload) {
      this.reload();
    }
  };

  showNewPredictorWin = () => {
    this.setState({
      newPredictorWinVisible: true
    });
    Mixpanel.track("Predictors > Train New");
  };

  showUploadWin = () => {
    this.setState({
      uploadWinVisible: true
    });
    Mixpanel.track("Predictors > Upload");
  };

  hideUploadWin = () => {
    this.setState(
      {
        uploadWinVisible: false
      },
      () => {
        this.reload();
      }
    );
  };

  actionTemplate = (rowData, _column) => (
    <div>
      <div className="minds-table__actions">
        <Button
          type="button"
          disabled={rowData.status === "training"}
          className="minds-action-btn lg-btn"
          label="QUERY"
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            navigation.openQuery(rowData.name);
            Mixpanel.track("Predictors > Query");
          }}
        />
        <Button
          type="button"
          disabled={rowData.status === "training"}
          className="minds-action-btn lg-btn"
          label="PREVIEW"
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            this.onSelectionChange({ value: rowData });
            Mixpanel.track("Predictors > Preview");
          }}
        />
        <Button
          type="button"
          disabled={rowData.status === "training"}
          className="minds-action-btn sm-btn"
          label={
            <span className="minds-action-btn__icon">
              <i className="fas fa-download" />
            </span>
          }
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            this.onDownload(rowData);
            Mixpanel.track("Predictors > Download");
          }}
        />
        <Button
          type="button"
          className="minds-action-btn red"
          label={
            <span className="minds-action-btn__icon">
              <i className="fas fa-trash" />
            </span>
          }
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            this.onDelete(rowData);
            Mixpanel.track("Predictors > Delete");
          }}
        />
      </div>
    </div>
  );

  fieldsTemplate = (rowData, column) => rowData[column.field].join(", ");

  accuracyTemplace = (rowData, column) => {
    let className;
    if (rowData[column.field] === 0) {
      return "-";
    }
    if (rowData["status"] === "complete") {
      className = "completed";
    }
    return (
      <span className={className}>
        {parseFloat((rowData[column.field] * 100).toFixed(2))}%
      </span>
    );
  };

  statusTemplate = (rowData, column) => {
    let className;
    let elapsed;
    if (rowData[column.field] === "complete") {
      className = "completed";
    }
    if (rowData[column.field] === "training") {
      let startDate = Moment(rowData.created_at);
      elapsed = Moment.duration(Moment().diff(startDate));
      elapsed = Moment.utc(elapsed.asMilliseconds()).format("HH:mm:ss");
    }
    return (
      <span className={className}>
        {rowData[column.field]} {elapsed}
      </span>
    );
  };

  render() {
    const { predictorsStore } = this.props;
    const { thWidths, thHeight } = this.state;
    const cols = [...predictorsStore.fields];
    const dynamicColumns = cols.map((col, index) => {
      let template;
      if (col.field === "predict") {
        template = this.fieldsTemplate;
      } else if (col.field === "accuracy") {
        template = this.accuracyTemplace;
      }
      if (col.field === "status") {
        template = this.statusTemplate;
      }
      return (
        <Column
          key={col.field}
          body={template}
          field={col.field}
          header={col.header}
          headerStyle={{
            width: thWidths ? thWidths[index] : undefined,
            maxWidth: thWidths ? thWidths[index] : undefined
          }}
          sortable
        />
      );
    });
    dynamicColumns.push(
      <Column
        body={this.actionTemplate}
        key="actions-column"
        style={{ textAlign: "center", width: "12em" }}
      />
    );

    return (
      <div className="main-content">
        <div className="header">
          <div className="content-dm">
            <div className="dm-items">
              <TitleBar>Predictors</TitleBar>
            </div>
            <DarkMode />
          </div>
        </div>
        <div
          className="main-content__body table-fit-scroll"
          style={{
            paddingBottom: "6rem",
            overflow: predictorsStore.data.length === 0 ? "hidden" : "auto"
          }}
          ref={r => {
            this.wrapperRef = r;
          }}
        >
          {this.props.predictorsStore.data.length === 0 ? (
            <EmptyPage
              topMessage="There is nothing here yet."
              bottomMessage="Upload model or click on train new"
            />
          ) : (
              <DataTable
                value={this.props.predictorsStore.data}
                headerHeight={thHeight}
                selectionMode="single"
                className="minds-table card predictors-datatable"
                onSelectionChange={this.onSelectionChange}
              >
                {dynamicColumns}
              </DataTable>
            )}
          <NewPredictorWin
            visible={this.state.newPredictorWinVisible}
            onHide={this.hideNewPredictorWin}
            dataSourcesStore={dataSourcesStore}
          />
          <UploadWin
            visible={this.state.uploadWinVisible}
            onHide={this.hideUploadWin}
          />
        </div>
        <div className="fixed-buttons">
          <Button
            className="minds-action-btn"
            type="button"
            icon="fas fa-dolly-flatbed"
            label="UPLOAD MODEL"
            onClick={this.showUploadWin}
          />
          <Button
            className="minds-action-btn"
            type="button"
            icon="fas fa-robot"
            label=" TRAIN NEW"
            onClick={this.showNewPredictorWin}
          />
        </div>
      </div>
    );
  }
}

export default Predictors;
