import React, { useState, useEffect } from 'react';
import { Button } from 'primereact/button';
import { Column } from 'primereact/column';
import MindsDB from 'mindsdb-js-sdk';

import { TitleBar } from '../ui/TittleBar';
import NotificationMsg from '../ui/NotificationMsg';
import DataTable from '../ui/mindsDataTable';

import DarkMode from '../ui/darkMode';
import { messageNotification } from '../../utils/funcs';
import EmptyPage from '../common/emptyPage';
import NeoDBConnectionWin from './newDbConnection';
import CreateDB from './newDataset';
import { Mixpanel } from '../../utils/mixpanel';
import { forEach } from 'lodash';

const sourceTemplate = (rowData, column) => {
  if (
    rowData['source_type'] !== 'file' ||
    typeof rowData[column.field] !== 'string'
  )
    return rowData[column.field];
  const v = rowData[column.field].split('/');
  return v[v.length - 1];
};
const cols = [
  {
    field: 'type',
    header: 'DATABASE',
    class: 'name',
  },
  {
    field: 'host',
    header: 'HOST',
    body: sourceTemplate,
  },
  {
    field: 'port',
    header: 'PORT',
    class: 'rows',
  },
  {
    field: 'user',
    header: 'USERNAME',
    class: 'rows',
  },
  {
    field: 'date_last_update',
    header: 'DATE LAST UPDATED',
    body: sourceTemplate,
    class: 'added',
  },
];

const Databases = () => {
  const [addDBWinVisible, setAddDBWinVisible] = useState(false);
  const [showCreateDB, setShowCreateDB] = useState(false);
  const [dbList, setDbList] = useState([]);
  const [messages, setMessages] = useState(null);
  const [isEdit, setIsEdit] = useState([]);

  const loadDBList = () => {
    MindsDB.DataBase()
      .load()
      .then((dbResponse) => {
        if (dbResponse.status === 200) {
          const resDatabases = [];
          forEach(dbResponse.data, (value, key) => {
            value.db_name = key;
            resDatabases.push(value);
          });
          setDbList(resDatabases);
        }
      })
      .catch((e) =>
        messageNotification(
          e.error.messages,
          'error',
          'Something wrong happens with the databases list',
          '',
          false
        )
      );
  };

  const showMessages = (type = 'success', message) => {
    messageNotification(messages, type, message, '', false);
  };

  useEffect(() => loadDBList(), []);

  const actionTemplate = (rowData, column) => {
    const removeDB = (e) => {
      e.preventDefault();
      e.stopPropagation();
      Mixpanel.track('Databases > Delete');
      MindsDB.DataBase()
        .delete(rowData)
        .then((ps) => {
          loadDBList();
          messageNotification(
            messages,
            'success',
            'Database removed',
            '',
            false
          );
        })
        .catch((e) =>
          messageNotification(
            messages,
            'error',
            'Database not found',
            '',
            false
          )
        );
    };

    return (
      <div className="minds-table__actions">
        <Button
          type="button"
          className="minds-action-btn lg-btn"
          label="New dataset"
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
            setIsEdit(rowData);
            setShowCreateDB(true);
          }}
        />
        <Button
          type="button"
          className="minds-action-btn lg-btn"
          label={
            <span className="minds-action-btn__icon">
              <i className={'fas fa-cog'} />
            </span>
          }
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
            setIsEdit(rowData);
            setAddDBWinVisible(true);
            Mixpanel.track('Databases > Edit');
          }}
        />
        <Button
          type="button"
          className="minds-action-btn red"
          label={
            <span className="minds-action-btn__icon">
              <i className={'fas fa-trash'} />
            </span>
          }
          onClick={(e) => removeDB(e)}
        />
      </div>
    );
  };

  const columns = cols.map((col, index) => (
    <Column
      key={col.field}
      {...col}
      sortable
      className={col.class}
      headerStyle={{
        width: undefined,
        maxWidth: undefined,
      }}
    />
  ));
  columns.push(<Column body={actionTemplate} key="actions-column" />);

  const shownewDBWin = () => {
    setAddDBWinVisible(true);
    setIsEdit([]);
  };
  const hideNewDBWin = () => {
    setAddDBWinVisible(false);
    loadDBList();
  };

  const onSelectionChange = (e) => {
    console.log(e.value);
  };

  return (
    <div className="main-content">
      <div className="header">
        <div className="content-dm">
          <div className="dm-items">
            <TitleBar>Databases</TitleBar>
          </div>
          <DarkMode />
        </div>
      </div>
      <NeoDBConnectionWin
        visible={addDBWinVisible}
        onHide={hideNewDBWin}
        isEdit={isEdit}
        showMessage={showMessages}
      />
      <NotificationMsg refElement={(el) => setMessages(el)} />
      {dbList.length > 0 ? (
        <DataTable
          value={dbList}
          headerHeight={10}
          className="minds-table card datasources-data-base-table border-datatable"
          selectionMode="single"
          onSelectionChange={onSelectionChange}
          loading={false}
        >
          {columns}
        </DataTable>
      ) : (
        <EmptyPage
          topMessage="There is nothing here yet."
          bottomMessage="Connect to a Database"
          customIcon="fa-plug"
          inProgressText="This feature is still a work in progess (Beta)"
          inProgressTextLink="Let us know what you think here"
          inProgressLink="https://community.mindsdb.com/"
        />
      )}

      <CreateDB
        visible={showCreateDB}
        isEdit={isEdit}
        onHide={() => setShowCreateDB(false)}
      />

      <div className="fixed-buttons">
        <Button
          className="minds-action-btn"
          type="button"
          label="CONNECT"
          icon="fas fa-exchange-alt"
          onClick={shownewDBWin}
        />
      </div>
    </div>
  );
};

export default Databases;
