import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Message } from 'primereact/message';
import { observer } from 'mobx-react';
import { Dropdown } from 'primereact/dropdown';
import { Button } from 'primereact/button';
import { Panel } from 'primereact/panel';
import { InputBlock } from './styles';
import CloudAuth from './cloudAuth';
import configStore from '../../store/config';
import './index.scss';
import { TitleBar } from '../ui/TittleBar';
import StatusBar from '../ui/StatusBar';
import DarkMode from '../ui/darkMode';
import { InputText } from '../inputText';

// const { shell, ipcRenderer } = require("electron");

@observer
class Config extends Component {
  static propTypes = {
    connectionStore: PropTypes.object.isRequired,
    configStore: PropTypes.object.isRequired,
    dataSourcesStore: PropTypes.object.isRequired,
    predictorsStore: PropTypes.object.isRequired,
  };

  state = {
    url: this.props.connectionStore.url,
    token: '',
    pypath: '',
    serverType: 'remote',
    msgFilter: ['info', 'data', 'error'],
    validUrl: true,
    loading: false,
  };

  // componentDidMount() {
  //   this.setState({
  //     pypath: this.props.configStore.config.python.path,
  //     serverType: this.props.configStore.config.defaultServerType,
  //   });
  // }

  configUpdateTimeout = null;

  setFieldValue = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    if (name === 'pypath' && value !== this.state.pypath) {
      if (this.configUpdateTimeout !== null) {
        clearTimeout(this.configUpdateTimeout);
      }
      this.configUpdateTimeout = setTimeout(() => {
        this.configUpdateTimeout = null;
        this.props.configStore.saveConfig({
          pypath: value,
        });
      }, 500);
    }

    let validUrl = true;
    if (name === 'url' && !this.isValidUrl(value)) {
      validUrl = false;
    }

    this.setState({
      [name]: value,
      validUrl,
    });
  };

  onConnect = () => {
    const { url, token, validUrl } = this.state;
    const { connectionStore, dataSourcesStore } = this.props;
    dataSourcesStore.reset();

    if (validUrl) {
      localStorage.setItem('apikey', token);
      connectionStore.connect(url, [{ key: 'apikey', value: token }]);
    }
  };

  onDisconnect = () => {
    const { connectionStore, dataSourcesStore, predictorsStore } = this.props;
    delete localStorage.apikey;
    connectionStore.disconnect();
    dataSourcesStore.reset();
    predictorsStore.reset();
  };

  onRunLocalServer = () => {
    try {
      this.onStopLocalServer();
    } catch (err) {
      console.log(err);
    }

    setTimeout(() => {
      const { configStore } = this.props;

      configStore.waitLocalServerStatusUpdate();
      // ipcRenderer.send("local-server-run");
    }, 1000);
  };

  onStopLocalServer = () => {
    this.props.configStore.waitLocalServerStatusUpdate();
    // ipcRenderer.send("local-server-stop");
  };

  onInstallLocalServer = () => {
    this.props.configStore.waitLocalServerStatusUpdate();
    // ipcRenderer.send("local-server-install");
  };

  onDeleteLocalServer = () => {
    this.props.configStore.waitLocalServerStatusUpdate();
    // ipcRenderer.send("local-server-delete");
  };

  onChangeServerType = (e) => {
    const { connected } = this.props.connectionStore;
    // if (connected) {
    //   window.growl.show({
    //     severity: "info",
    //     summary: "notice",
    //     detail: "need to disconnect from current server",
    //   });
    //   return;
    // }
    this.props.configStore.saveConfig({
      defaultServerType: e.value,
    });
    this.setState({ serverType: e.value });
  };

  onPythonDownloadClick = (e) => {
    e.preventDefault();
    // shell.openExternal("https://www.python.org/downloads/");
  };

  selectFilter = (e) => {
    const { msgFilter } = this.state;
    const index = msgFilter.indexOf(e.name);
    if (index >= 0) {
      const newValues = [...msgFilter];
      newValues.splice(index, 1);
      this.setState({
        msgFilter: newValues,
      });
    } else {
      this.setState({
        msgFilter: [...msgFilter, e.name],
      });
    }
  };

  openLink = (url) => {
    // require("electron").shell.openExternal(url);
  };

  isValidUrl = (value) => {
    return /^(?:^|\s)((https?:\/\/)?(?:localhost|[\w-]+(?:\.[\w-]+)+)(:\d+)?(\/\S*)?)?$/i.test(
      value
    );
  };

  onCloudConnected = ({ token }) => {
    this.props.connectionStore.connect(configStore.cloudUrl, [
      { key: 'apikey', value: token },
    ]);
  };

  setLoading = (loading) => {
    this.setState({ loading });
  };

  render() {
    const { connectionStore, configStore } = this.props;
    const {
      localServerStatus,
      waitingLocalServerStatusUpdate,
    } = configStore.config;
    const { connected } = connectionStore;
    const {
      validUrl,
      serverType,
      pypath,
      msgFilter,
      url,
      token,
      loading,
    } = this.state;
    let serverStatus = '';
    if (
      localServerStatus.installed === false &&
      localServerStatus.installing === false
    ) {
      serverStatus = 'Not installed';
    } else if (localServerStatus.installing === true) {
      serverStatus = 'Installing';
    } else if (localServerStatus.starting === true) {
      serverStatus = 'Starting';
    } else if (localServerStatus.running === true) {
      serverStatus = 'Running';
    } else {
      serverStatus = 'Installed';
    }
    return (
      <div className="main-content">
        <div className="header">
          <div className="content-dm">
            <div className="dm-items">
              <TitleBar>Connection</TitleBar>
              <StatusBar
                isconnected={connected}
                serverType={serverType}
                serverStatus={serverStatus}
              />
            </div>
            <DarkMode />
          </div>
        </div>
        <div className="main-content__body">
          <Panel
            className="minds-block"
            style={{
              margin: 20,
              flex: 1,
              display: 'flex',
              height: 'calc(100% - 40px)',
              overflow: 'hidden',
            }}
          >
            <div className="connectionMain">
              {/* <div className="connection-top">
                <div className="connect-choose width30">
                  <span>Where to Run</span>
                  {connected && serverType === "local" ? (
                    "Locally"
                  ) : (
                    <Dropdown
                      value={serverType}
                      className="mdb-select"
                      options={[
                        { label: "On a Remote Server", value: "remote" },
                        { label: "Locally", value: "local" },
                        // { label: "MindsDB Cloud", value: "cloud" },
                      ]}
                      onChange={this.onChangeServerType}
                      disabled={loading}
                      placeholder=""
                    />
                  )}
                </div>
              </div> */}
              <div>
                <div className="config-holder__local-server-status"></div>
                <div className="server-buttons">
                  {connected && serverType === 'remote' && (
                    <Button
                      className={`minds-action-btn red  ${
                        serverType === 'remote' ? 'abs-btn-position' : ''
                      }`}
                      type="button"
                      label="DISCONNECT"
                      icon="fas fa-not-equal"
                      onClick={this.onDisconnect}
                    />
                  )}
                  <Button
                    className={`minds-action-btn ${
                      serverType === 'remote' ? 'abs-btn-position' : ''
                    }`}
                    type="button"
                    label="CONNECT"
                    icon="fas fa-exchange-alt"
                      style={{
                        display:
                          (!connected) || (this.props.configStore.config.defaultServerType === "remote" && !connected)
                            ? "block"
                            : "none",
                      }}
                      onClick={this.onConnect}
                    />
                  
                  {localServerStatus.installed}
                </div>
              </div>
              <div
                className="config-holder"
                style={{
                  display: serverType === 'local' ? 'block' : 'none',
                }}
              >
                {/* START */}
                <div
                  className="config-holder__local-server-install"
                  style={{
                    display:
                      localServerStatus.installing ||
                      localServerStatus.installed
                        ? 'none'
                        : 'block',
                  }}
                >
                  <InputBlock>
                    <div className="config-holder__local-server-install__pypath">
                      <span className="input-title">Python Path:</span>
                      <div className="input-container">
                        <InputText
                          name="pypath"
                          placeholder=""
                          className={`${
                            configStore.isGoodPython === false
                              ? 'error-input'
                              : ''
                          }`}
                          value={pypath}
                          onChange={this.setFieldValue}
                          style={{ width: 100 }}
                        />
                        {configStore.isGoodPython === true && (
                          <Message
                            className="overwrite-p-message"
                            severity="success"
                            text=""
                          />
                        )}
                        {/* {configStore.isGoodPython === false &&
                          configStore.config.python.isPython === true && (
                            <Message
                              severity="error"
                              text={`wrong version${
                                typeof configStore.config.python.version ===
                                  "string" &&
                                configStore.config.python.version.length > 0
                                  ? `: ${configStore.config.python.version} < ${configStore.goodPythonVersion}`
                                  : ""
                              }`}
                            />
                          )} */}
                        {/* {configStore.isGoodPython === false &&
                          configStore.config.python.isPython === false && (
                            <Message className="not-python" severity="error" />
                          )} */}
                      </div>
                      {configStore.isGoodPython === false && (
                        <div>
                          <p className="download-python">
                            You can download Python&nbsp;
                            <a
                              role="button"
                              href="https://www.python.org/downloads/"
                              onClick={this.onPythonDownloadClick}
                            >
                              here
                            </a>
                          </p>
                        </div>
                      )}
                      <p>
                        At this time, the local server cant run because it is
                        not installed. This installation will take about five
                        minutes and require at least 1.2 GB of free disk space.
                        During the installation process, significant amounts of
                        data can be downloaded from the internet. Python 3.6 (or
                        higher) is also required to start. Please print path to
                        Python as follows:
                      </p>
                      <a
                        href="/"
                        onClick={() =>
                          this.openLink(
                            'https://community.mindsdb.com/t/how-to-find-python-path-information/37'
                          )
                        }
                      >
                        Where can I find this?
                      </a>
                    </div>
                  </InputBlock>
                </div>
                {/* STOP */}
                <div
                  id="terminal"
                  className="server-log__body"
                  style={{
                    display:
                      localServerStatus.installed ||
                      localServerStatus.installing
                        ? undefined
                        : 'none',
                  }}
                >
                  {configStore.log.reduce((prev, _next, index) => {
                    const reverseIndex = configStore.log.length - index - 1;
                    const reverseElement = configStore.log[reverseIndex];
                    const terminalDiv = document.getElementById('terminal');
                    let colorMessage = '__msg';
                    if (reverseElement.type === 'error') {
                      if (!reverseElement.data.includes('INFO')) {
                        colorMessage = reverseElement.data.includes('WARNING')
                          ? 'warning'
                          : reverseElement.data.includes('DEBUG')
                          ? 'warning'
                          : 'error';
                      }
                    } else if (reverseElement.type === 'info') {
                      colorMessage = 'info';
                    }

                    if (msgFilter.indexOf(reverseElement.type) !== -1) {
                      prev.push(
                        <p
                          key={reverseIndex}
                          className={`server-log__body__msg server-log__body__msg-${colorMessage}`}
                        >
                          {`$terminal: ${reverseElement.data}`}
                        </p>
                      );
                    }

                    if (terminalDiv) {
                      terminalDiv.scrollTop = terminalDiv.scrollHeight;
                    }
                    return prev.sort((a, b) => a.key - b.key);
                  }, [])}
                </div>
                <div className="server-buttons">
                  {localServerStatus.installed === true && (
                    <Button
                      type="button"
                      className="minds-action-btn"
                      label="CONNECT"
                      icon="fas fa-exchange-alt"
                      onClick={this.onRunLocalServer}
                      disabled={
                        connected ||
                        localServerStatus.starting ||
                        localServerStatus.installed === false ||
                        waitingLocalServerStatusUpdate
                      }
                    />
                  )}
                  {localServerStatus.running === true && (
                    <Button
                      className="minds-action-btn red"
                      type="button"
                      label="STOP"
                      icon="fas fa-ban"
                      onClick={this.onStopLocalServer}
                      disabled={waitingLocalServerStatusUpdate}
                    />
                  )}
                  {localServerStatus.installed === true && (
                    <Button
                      className="minds-action-btn red"
                      type="button"
                      label="DELETE"
                      icon="fas fa-link"
                      onClick={this.onDeleteLocalServer}
                      disabled={
                        localServerStatus.running ||
                        localServerStatus.starting ||
                        waitingLocalServerStatusUpdate
                      }
                    />
                  )}
                </div>
                <div
                  className="server-log-install"
                  style={{
                    display:
                      localServerStatus.installed ||
                      localServerStatus.installing
                        ? 'none'
                        : 'block',
                  }}
                >
                  {localServerStatus.installed === false && (
                    <Button
                      className="minds-action-btn"
                      type="button"
                      label="INSTALL"
                      icon="fas fa-link"
                      onClick={this.onInstallLocalServer}
                      disabled={
                        localServerStatus.installing === true ||
                        configStore.isGoodPython === false ||
                        waitingLocalServerStatusUpdate
                      }
                    />
                  )}
                </div>
              </div>
              <div
                className="config-holder"
                style={{
                  display: serverType === 'cloud' ? 'block' : 'none',
                }}
              >
                <CloudAuth
                  onConnect={this.onCloudConnected}
                  onSetLoading={this.setLoading}
                  connected={connected}
                  loading={loading}
                />
              </div>
            </div>
            <div className="remote-inputs">
              <div
                className="connect-choose"
                style={{
                  display: serverType === 'remote' ? 'block' : 'none',
                }}
              >
                <span>URL</span>
                <InputText
                  name="url"
                  placeholder="url"
                  className={`mdb-input  ${!validUrl ? 'inputError' : ''}`}
                  value={url}
                  onChange={this.setFieldValue}
                />
                {!validUrl && (
                  <strong className="valid_url">
                    Please enter a valid URL
                  </strong>
                )}
              </div>
              <div
                className="connect-choose"
                style={{
                  display: serverType === 'remote' ? 'block' : 'none',
                }}
              >
                <span>Token</span>
                <InputText
                  name="token"
                  placeholder="Place Your Token Here"
                  className="mdb-input"
                  value={token}
                  onChange={this.setFieldValue}
                />
              </div>
              {serverType === 'remote' && (
                <p>
                  MindsDB can run on a remote computer. All you need to provide
                  is the server URL and a security token (optional).
                </p>
              )}
            </div>
          </Panel>
        </div>
      </div>
    );
  }
}

export default Config;
