import React from 'react';

import withContext from '../../../HOC/withContext';
import { SidebarContainer } from './styles';

const SidebarContent = (props) => {
  const { context } = props;
  const openLink = (url) => {
    // require('electron').shell.openExternal(url);
  }

  return (
    <>
      {context && context.sidebarContent && (
        <SidebarContainer>
          <strong>{context.sidebarContent.title}</strong>
          <p>
            {context.sidebarContent.paragraph}
            {context.sidebarContent.url && (
              <a
                href="/"
                onClick={() =>
                  openLink(context.sidebarContent.url)
                }
              >
                Read more
              </a>
            )}
          </p>
        </SidebarContainer>)
      }
    </>
)};

export default withContext(SidebarContent);
