import React, { Component } from "react";
import PropTypes from "prop-types";
import MindsDB from "mindsdb-js-sdk";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import { observer } from "mobx-react";
import DataTable from '../ui/mindsDataTable';
import UrlWin from "./urlWin";
import UploadWin from "./uploadWin";
import DeleteWin from "./deleteWin";
import { utcStringToUSDateTimeString } from "../../utils/funcs";
import EmptyPage from "../common/emptyPage";
import navigation from "../../store/navigation";
import { Mixpanel } from "../../utils/mixpanel";
import { TitleBar } from "../ui/TittleBar"
import DarkMode from "../ui/darkMode";

import "./index.scss";

// const ipcRenderer = require("electron").ipcRenderer;

@observer
class Datasources extends Component {
  static propTypes = {
    dataSourcesStore: PropTypes.object.isRequired
  };

  state = {
    urlWinVisible: false,
    uploadWinVisible: false,
    deleteWinVisible: false,

    deletingName: null,

    thWidths: null,
    thHeight: null
  };

  interval = null;

  componentWillMount() {
    // ipcRenderer.on("fileReaded", this.onFileReaded);
    const { dataSourcesStore } = this.props;

    if (
      dataSourcesStore.loaded === false &&
      dataSourcesStore.loading === false
    ) {
      dataSourcesStore.load();
    }
    this.interval = setInterval(() => {
      if (dataSourcesStore.loading === false) {
        dataSourcesStore.load();
      }
    }, 5000);
  }

  componentDidMount() {
    this.resizeObserver = new ResizeObserver(this.onResize);
    this.mutationObserver = new MutationObserver(this.onResize);
    this.resizeObserver.observe(this.wrapperRef);
    this.mutationObserver.observe(this.wrapperRef, {
      childList: true,
      subtree: true
    });
  }

  componentWillUnmount() {
    this.resizeObserver.disconnect();
    this.mutationObserver.disconnect();
    // ipcRenderer.removeListener("fileReaded", this.onFileReaded);
    clearInterval(this.interval);
  }

  onResize = () => {
    // get cells widths
    const row = this.wrapperRef.querySelector(".p-datatable-row");
    let thWidths = null;
    let thHeight = null;
    if (row) {
      thWidths = new Array(row.children.length);
      for (let i = 0; i < row.children.length; i++) {
        thWidths[i] = row.children[i].getClientRects()[0].width;
      }
    }
    // get title height
    const header = this.wrapperRef.querySelector(".p-datatable-thead > tr");
    if (header) {
      thHeight = header.getBoundingClientRect().height;
    }
    this.setState({
      thWidths,
      thHeight
    });
  };

  fileReadingPromises = {};

  onFileReaded = async (event, args) => {
    if (args.err != null) {
      if (args.err === "ENOENT") {
        // window.growl.show({
        //   severity: "error",
        //   summary: "error",
        //   detail: "File not exist"
        // });
      } else {
        // window.growl.show({
        //   severity: "error",
        //   summary: "error",
        //   detail: `Unknown error: ${args.err}`
        // });
      }
      return;
    }
    const file = new Blob([args.file]);

    await MindsDB.DataSource({ name: args.datasourceName }).uploadFile({
      column: args.columnName,
      rowIndex: args.index,
      extension: args.extension,
      file
    });

    const promiseName = `${args.datasourceName}:${args.columnName}:${args.index}`;
    this.fileReadingPromises[promiseName]();
    delete this.fileReadingPromises[promiseName];
  };

  reload = async () => {
    const { dataSourcesStore } = this.props;

    await dataSourcesStore.load();
  };

  hideUrlWin = needReload => {
    this.setState({
      urlWinVisible: false
    });
    if (needReload) {
      this.reload();
    }
  };

  showUrlWin = () => {
    this.setState({
      urlWinVisible: true
    });
    Mixpanel.track("Datasources > Upload By Url");
  };

  hideUploadWin = async (needReload, newDataSourceName) => {
    this.setState({
      uploadWinVisible: false
    });
    if (typeof needReload === "boolean" && needReload === true) {
      await this.reload();
      const newDS = this.props.dataSourcesStore.getByName(newDataSourceName);
      if (newDS == null)
        throw new Error(
          `${this.constructor.name}.hideUploadWin: cant find DS with name ${newDataSourceName}`
        );
      if (newDS["missed_files"] === true) {
        await newDS.loadMissedFileList();
        for (let i = 0; i < newDS.missedFileList.data.length; i++) {
          const path = newDS.missedFileList.data[i].path;
          const datasourceName = newDS.name;
          const columnName = newDS.missedFileList.data[i]["column_name"];
          const index = newDS.missedFileList.data[i].index;
          const code = await new Promise(resolve => {
            this.fileReadingPromises[
              `${datasourceName}:${columnName}:${index}`
            ] = resolve;
            // ipcRenderer.send("readFile", {
            //   datasourceName,
            //   columnName,
            //   index,
            //   path
            // });
          });
          if (code !== 200) {
            // window.growl.show({
            //   severity: "error",
            //   summary: "error",
            //   detail: `Cant upload file ${path}`
            // });
          }
        }
      }
    }
  };

  showUploadWin = () => {
    this.setState({
      uploadWinVisible: true
    });
    Mixpanel.track("Datasources > Upload File");
  };

  showDeleteWin = name => {
    this.setState({
      deleteWinVisible: true,
      deletingName: name
    });
  };

  hideDeleteWin = () => {
    this.setState({
      deleteWinVisible: false,
      deletingName: null
    });
  };

  /* eslint-disable */
  actionTemplate = (rowData, column) => {
    return (
      <div className="minds-table__actions">
        <Button
          type="button"
          className="minds-action-btn lg-btn"
          label="QUALITY"
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            navigation.openDataQuality(rowData);
            Mixpanel.track("Datasources > Quality");
          }}
        ></Button>
        <Button
          type="button"
          className="minds-action-btn lg-btn"
          label="PREVIEW"
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            navigation.openDataPreview(rowData);
            Mixpanel.track("Datasources > Preview");
          }}
        ></Button>
        <Button
          type="button"
          className="minds-action-btn sm-btn"
          label={
            <span className="minds-action-btn__icon">
              <i className={"fas fa-download"} />
            </span>
          }
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();

            this.props.dataSourcesStore.download({
              name: rowData.name,
              source_type: rowData["source_type"],
              source: rowData.source
            });
            Mixpanel.track("Datasources > Download");
          }}
        ></Button>
        <Button
          type="button"
          className="minds-action-btn red"
          label={
            <span className="minds-action-btn__icon">
              <i className={"fas fa-trash"} />
            </span>
          }
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            this.showDeleteWin(rowData.name);
            Mixpanel.track("Datasources > Delete");
          }}
        ></Button>
      </div>
    );
  };
  /* eslint-enable */

  onDeleteDatasource = name => {
    this.props.dataSourcesStore.delete({ name });

    this.hideDeleteWin();
  };

  onSelectionChange = e => {
    navigation.openDataQuality(e.value);
  };

  dtTemplate = (rowData, column) => {
    if (typeof rowData[column.field] !== "string") return rowData[column.field];
    return utcStringToUSDateTimeString(rowData[column.field]);
  };

  sourceTemplate = (rowData, column) => {
    if (
      rowData["source_type"] !== "file" ||
      typeof rowData[column.field] !== "string"
    )
      return rowData[column.field];
    const v = rowData[column.field].split("/");
    return v[v.length - 1];
  };

  render() {
    const { dataSourcesStore } = this.props;
    const { thWidths, thHeight } = this.state;

    // const { verticalScrollExists } = this.state;
    const cols = [
      {
        field: "name",
        header: "NAME",
        class: "name"
      },
      {
        field: "source",
        header: "SOURCE",
        body: this.sourceTemplate
      },
      {
        field: "row_count",
        header: "ROWS",
        class: "rows"
      },
      {
        field: "created_at",
        header: "DATE ADDED",
        body: this.dtTemplate,
        class: "added"
      }
    ];

    const columns = cols.map((col, index) => (
      <Column
        key={col.field}
        {...col}
        sortable
        className={col.class}
        headerStyle={{
          width: thWidths ? thWidths[index] : undefined,
          maxWidth: thWidths ? thWidths[index] : undefined
        }}
      />
    ));
    columns.push(
      <Column
        body={this.actionTemplate}
        key="actions-column"
        style={{ textAlign: "center", width: "12em" }}
      />
    );

    return (
      <div className="main-content">
        <div className="header">
          <div className="content-dm">
            <div className="dm-items">
              <TitleBar>Datasets</TitleBar>
            </div>
            <DarkMode />
          </div>
        </div>
        <div
          className="main-content__body table-fit-scroll"
          style={{
            paddingBottom: "6rem",
            overflow: dataSourcesStore.data.length === 0 ? "hidden" : "auto"
          }}
          ref={r => {
            this.wrapperRef = r;
          }}
        >
          {dataSourcesStore.data.length === 0 ? (
            <EmptyPage
              topMessage="There is nothing here yet."
              bottomMessage="Upload your first datasource to begin"
            />
          ) : (
              <DataTable
                value={dataSourcesStore.data}
                headerHeight={thHeight}
                className="minds-table card datasources-datatable"
                selectionMode="single"
                onSelectionChange={this.onSelectionChange}
                loading={dataSourcesStore.loading}
              >
                {columns}
              </DataTable>
            )}
          <UrlWin visible={this.state.urlWinVisible} onHide={this.hideUrlWin} />
          <UploadWin
            visible={this.state.uploadWinVisible}
            onHide={this.hideUploadWin}
          />
          <DeleteWin
            visible={this.state.deleteWinVisible}
            onHide={this.hideDeleteWin}
            onDelete={this.onDeleteDatasource}
            name={this.state.deletingName}
          />
        </div>
        <div className="fixed-buttons">
          <Button
            className="minds-action-btn"
            type="button"
            icon="fas fa-dolly-flatbed"
            label="     UPLOAD"
            onClick={this.showUploadWin}
          />
          <Button
            className="minds-action-btn"
            type="button"
            icon="fas fa-link"
            label="ADD BY URL"
            onClick={this.showUrlWin}
          />
          <Button
            className="minds-action-btn"
            type="button"
            icon="fas fa-redo"
            label="REFRESH"
            onClick={this.reload}
          />
        </div>
      </div>
    );
  }
}

export default Datasources;
