import React from 'react';
import styled from '@emotion/styled';
import { useSpring, animated } from 'react-spring';
import { ResponsiveHeatMap } from '@nivo/heatmap';

import { Colors } from '../Colors';

const ContainerToolTips = styled.div`
  cursor: pointer;
  display: inline-flex;
  flex-direction: row;
  align-items: baseline;
  span {
    margin-right: 5px;
    margin-left: 5px;
  }
  .redSpan {
    color: ${Colors.watermelon};
  }
  .greenSpan {
    color: ${Colors.selectedBreacump};
  }
`
const TootltipSquare = styled.div`
  width: 10px;
  height: 10px;
  margin-right: 5px;
  &.redSquare {
    background: ${Colors.watermelon};
  }
  &.greenSquare {
    background: ${Colors.selectedBreacump};
  }
`

export const ConfMatrix = ({ dataResponse, dataKeys, hoverEvent }) => {
  const CustomCell = ({
    x,
    y,
    data,
    value,
    width,
    height,
    color,
    opacity,
    borderWidth,
    borderColor,
    textColor,
    onHover,
    onLeave,
  }) => {
    const isMiddle = data.xKey === data.yKey;
    const cell = parseFloat(value * 100).toFixed(0);

    const animatedProps = useSpring({
      transform: `translate(${x}, ${y})`,
      width,
      height,
      xOffset: width * -0.5,
      yOffset: height * -0.6,
      color,
      opacity,
      textColor,
      borderWidth,
      borderColor,
    });

    return (
      <>
        <animated.g
          transform={animatedProps.transform}
          style={{ cursor: 'pointer' }}
          onMouseEnter={() => {
            if (value > 0) {
              hoverEvent({
                cell: cell,
                predictedField: data.yKey,
                actual: data.xKey,
                validColumn: isMiddle
              })
            }
          }
          }
          onMouseMove={onHover}
          onMouseLeave={onLeave}
        >
          <animated.rect
            x={animatedProps.xOffset}
            y={animatedProps.yOffset}
            width={animatedProps.width}
            height={animatedProps.height}
            fill={(value === 0 || isNaN(value)) ? "white" : isMiddle
              ? `rgb(0, 176, 109, ${cell / 100} )`
              : `rgb(255, 0, 0, ${cell / 100})`
            }
            strokeWidth={animatedProps.borderWidth}
            stroke={animatedProps.borderColor}
            strokeOpacity={animatedProps.opacity}
          />
          <animated.text
            dominantBaseline="central"
            textAnchor="middle"
            style={{ fill: textColor }}
            dy={value < 50 ? -6 : 6}
          >
            {
              (value === 0 || isNaN(value)) ? '-' : `${cell}%`
            }
          </animated.text>
        </animated.g>
      </>
    )
  }

  return (
    <ResponsiveHeatMap
      onHover={hoverEvent}
      data={dataResponse}
      cellShape={CustomCell}
      keys={dataKeys}
      indexBy="predictedField"
      margin={{ top: 100, right: 60, bottom: 60, left: 60 }}
      forceSquare={true}
      animate={true}
      motionStiffness={80}
      motionDamping={9}
      hoverTarget="cell"
      axisTop={{
        orient: 'top',
        tickSize: 10,
        tickPadding: 5,
        tickRotation: -55,
        legend: 'Actual',
        legendPosition: 'start',
        legendOffset: -60,
      }}
      axisRight={null}
      axisBottom={null}
      axisLeft={{
        orient: 'left',
        tickSize: 10,
        tickPadding: 5,
        tickRotation: 0,
        legend: 'Predicted',
        legendPosition: 'middle',
        legendOffset: -70
      }}
      tooltip={({ value, xKey, yKey }) => {
        const validColumn = xKey === yKey;
        const cell = parseFloat(value * 100).toFixed(0);

        return (
          <ContainerToolTips>
            <TootltipSquare className={validColumn ? 'greenSquare': 'redSquare'} />
              <span className={validColumn ? 'greenSpan' : 'redSpan'}>
                {yKey}
              </span>
              {' - '}
              <span className={
                cell < 100 ? 'greenSpan'
                  : validColumn ? 'greenSpan' : 'redSpan' }
              >
                {xKey}
              </span>
          </ContainerToolTips>
        )
      }}
      cellOpacity={1}
      cellBorderColor={{ from: 'color', modifiers: [['darker', 1]] }}
      labelTextColor={{ from: 'color', modifiers: [['darker', 2]] }}
      cellHoverOthersOpacity={0.5}
    />
  )
};
