import React, { Component } from "react";
import PropTypes from "prop-types";
import MindsDB from "mindsdb-js-sdk";
import { Dialog } from "primereact/dialog";
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import { observer } from "mobx-react";
import { InputText } from '../../inputText'

import SimpleTable from "../../common/simpleTable";
import AdvancedMode from "./advancedMode";

@observer
class NewPredictorWin extends Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    onHide: PropTypes.func.isRequired,
    dataSourcesStore: PropTypes.object.isRequired
  };

  state = {
    dataSource: null,
    predictedFields: null,
    predictorName: "",
    showAdvancedMode: false,
    marginOfError: 0,
    stopTrainingAfter: null,
    backend: "lightwood",
    useGpu: false,
    optionalAnalysis: false,
    removeForTrainingFields: null
  };

  async componentWillMount() {
    const { dataSourcesStore } = this.props;
    if (!dataSourcesStore.loaded) {
      await dataSourcesStore.load();
    }
    this.selectDataSource();
  }

  selectDataSource() {
    const { dataSourcesStore } = this.props;
    if (
      dataSourcesStore.dropdownOptions &&
      dataSourcesStore.dropdownOptions.length
    ) {
      this.setState({ dataSource: dataSourcesStore.dropdownOptions[0].value });
    }
  }

  onShow = () => {
    this.setState({
      dataSource: null,
      predictedFields: null,
      predictorName: "",
      showAdvancedMode: false,
      marginOfError: 0,
      stopTrainingAfter: null,
      backend: "lightwood",
      useGpu: false,
      optionalAnalysis: false,
      removeForTrainingFields: null
    });
    this.selectDataSource();
  };

  onTrain = async () => {
    const { dataSourcesStore, onHide } = this.props;
    const {
      predictorName,
      dataSource,
      predictedFields,
      showAdvancedMode,
      marginOfError,
      stopTrainingAfter,
      backend,
      useGpu,
      removeForTrainingFields
    } = this.state;

    const dataSourceName = dataSourcesStore.data[dataSource].name;
    const toPredict = predictedFields;
    const predictor = MindsDB.Predictor({ name: predictorName });

    let dataToLearn = { dataSourceName, toPredict };
    if (showAdvancedMode) {
      const fieldsToIgnore = removeForTrainingFields
        ? removeForTrainingFields.filter(
            ignore => !predictedFields.find(predict => predict === ignore)
          )
        : null;

      const kwargs = {
        use_gpu: useGpu,
        backend: backend,
        ignore_columns: fieldsToIgnore,
        sample_settings: {
          sample_margin_of_error: parseFloat(marginOfError),
        },
        stop_training_in_x_seconds: stopTrainingAfter ? parseInt(stopTrainingAfter) * 60 * 1000 : 0, // to milliseconds
      };

      if (kwargs.stop_training_in_x_seconds === 0) {
        delete kwargs.stop_training_in_x_seconds;
      }

      dataToLearn = { dataSourceName, toPredict, kwargs };
    }

    try {
      predictor.learn(dataToLearn);
      // window.growl.show({ severity: "success", detail: "Training started" });
      onHide(true);
    } catch (e) {
      // window.showError(e);
    }
  };

  setFieldValue = e => {
    let { removeForTrainingFields } = this.state;
    if (e.target.name === "predictedFields" && removeForTrainingFields) {
      removeForTrainingFields = removeForTrainingFields.filter(
        ignore => !e.target.value.find(predict => predict === ignore)
      );
      this.setState({
        [e.target.name]: e.target.value,
        removeForTrainingFields
      });
    } else if (e.target.name === 'marginOfError') {
      this.setState({ [e.target.name]: e.target.value || 0 });
    } else {
      this.setState({ [e.target.name]: e.target.value });
    }
  };

  isInvalidMarginOfError = val => {
    if (!isNaN(val)) {
      const decimal = parseFloat(val);
      if ((decimal >= 0.05 && decimal <= 0.2) || decimal === 0) {
        return false;
      }
    }

    return true;
  };

  render() {
    const { dataSourcesStore, visible, onHide } = this.props;
    const {
      predictedFields,
      dataSource,
      predictorName,
      showAdvancedMode,
      marginOfError,
      stopTrainingAfter,
      backend,
      useGpu,
      optionalAnalysis,
      removeForTrainingFields
    } = this.state;
    let fields = [];
    if (dataSource !== null) {
      fields = dataSourcesStore
        .getFields(dataSource)
        .map(el => ({ label: el.name, value: el.name }));
    }
    let invalid =
      !predictedFields ||
      !predictedFields.length ||
      dataSource === null ||
      predictorName === "";

    if (showAdvancedMode) {
      invalid =
        invalid ||
        this.isInvalidMarginOfError(marginOfError) ||
        backend === null;
    }

    let fieldsToIgnore = fields;
    if (predictedFields && predictedFields.length > 0) {
      fieldsToIgnore = fields.filter(
        field => !predictedFields.find(predicted => predicted === field.value)
      );
    }

    return (
      <Dialog
        visible={visible}
        onHide={onHide}
        onShow={this.onShow}
        modal
        header={showAdvancedMode ? "New Predictor Adv. Mode" : "New Predictor"}
        className={`minds-dialog ${showAdvancedMode ? "width-100" : ""}`}
        style={{ width: showAdvancedMode ? "1000px" : "500px" }}
      >
        <div className={showAdvancedMode ? "super-container" : ""}>
          <div className="left-container">
            <div className="minds-dialog__field">
              <label htmlFor="cb3" className="p-checkbox-label">
                From:
              </label>
              <Dropdown
                inputId="cb3"
                name="dataSource"
                value={dataSource}
                // options={ dataSources }
                options={dataSourcesStore.dropdownOptions}
                onChange={this.setFieldValue}
                placeholder=""
                className="mdb-select"
              />
            </div>

            <div className="minds-dialog__field">
              <label htmlFor="cb5" className="p-checkbox-label">
                Predictor name:
              </label>
              <InputText
                id="cb5"
                name="predictorName"
                value={predictorName}
                onChange={this.setFieldValue}
                className="mdb-input"
                placeholder="Name Your New Predictor"
              />
            </div>

            <div className="minds-dialog__field">
              <label htmlFor="cb4" className="p-checkbox-label">
                Select only the colums to be predicted:
              </label>
              <SimpleTable
                name="predictedFields"
                filter
                value={predictedFields}
                options={fields}
                onChange={this.setFieldValue}
                searchIconStyles={showAdvancedMode ? { right: "8px" } : {}}
              />
            </div>
          </div>
          {showAdvancedMode ? (
            <div className="right-container">
              <AdvancedMode
                predictedFields={predictedFields}
                fields={fieldsToIgnore}
                onChange={this.setFieldValue}
                marginOfError={marginOfError}
                stopTrainingAfter={stopTrainingAfter}
                backend={backend}
                useGpu={useGpu}
                optionalAnalysis={optionalAnalysis}
                removeForTrainingFields={removeForTrainingFields}
              />
            </div>
          ) : null}
        </div>

        <div className="separate-btns" style={{ marginTop: "24px" }}>
          <p
            className="mode-button"
            onClick={() =>
              this.setState({ showAdvancedMode: !showAdvancedMode })
            }
          >
            {showAdvancedMode ? "Basic Mode" : "Advanced Mode"}
          </p>
          <div>
            <Button
              className="minds-action-btn lg-btn-spacing bold red"
              type="button"
              label={
                <div className="icon-text">
                  <span>CANCEL</span>
                </div>
              }
              onClick={onHide}
            />
            <Button
              className="minds-action-btn lg-btn-spacing bold"
              type="button"
              label={
                <div className="icon-text">
                  <span>TRAIN</span>
                </div>
              }
              onClick={this.onTrain}
              disabled={invalid}
            />
          </div>
        </div>
      </Dialog>
    );
  }
}

export default NewPredictorWin;
