import React, { Component } from "react";
import PropTypes from "prop-types";
import { observer } from "mobx-react";
import { Button } from "primereact/button";
import { Mixpanel } from "../../utils/mixpanel";
import { TitleBar } from '../ui/TittleBar';

import NewQuery from "./newQuery";
import QueryResult from "./queryResult";

import EmptyPage from "../common/emptyPage";

import predictorsStore from "../../store/predictors";
import predictorStore from "../../store/predictor";
import ErrorBoundary from "../common/utils/ErrorBoundary";
import DarkMode from "../ui/darkMode";

import "./index.scss";

@observer
class Query extends Component {
  static propTypes = {
    preselectedPredictorName: PropTypes.string
  };

  static defaultProps = {
    preselectedPredictorName: null
  };

  state = {
    newQueryVisible: false
  };

  showNewQuery = () => {
    this.setState({
      newQueryVisible: true
    });
    Mixpanel.track("Query > Create New one");
  };

  hideNewQuery = needReload => {
    this.setState({
      newQueryVisible: false
    });
    if (needReload) {
      this.reload();
    }
  };

  reload = () => {
    console.log("reload");
  };

  render() {
    const { queries } = predictorsStore;
    const { preselectedPredictorName } = this.props;
    const { newQueryVisible } = this.state;

    const queryResults = [];
    for (let index = queries.length - 1; index >= 0; index--) {
      queryResults.push(
        <QueryResult
          key={`query-${index}`}
          query={queries[index]}
          index={index}
          predictorsStore={predictorsStore}
        />
      );
    }

    return (
      <ErrorBoundary fullScreen fullPosition >
        <div className="main-content">
          <div className="header">
            <div className="content-dm">
              <div className="dm-items">
                <TitleBar>Query</TitleBar>
              </div>
              <DarkMode />
            </div>
          </div>
          {queryResults.length === 0 ? (
            <>
              <div
                className="main-content__body table-fit-scroll"
                style={{
                  paddingBottom: "6rem",
                  overflow: queryResults.length === 0 ? "hidden" : "auto"
                }}
              >
                <EmptyPage
                  topMessage="There is nothing here yet."
                  bottomMessage="Queries allow you to make predictions for trained models, Click on New Query to get started."
                />
              </div>

              <div className="fixed-buttons">
                <Button
                  className="minds-action-btn"
                  type="button"
                  icon="fas fa-question"
                  label="NEW QUERY"
                  onClick={this.showNewQuery}
                />
              </div>
            </>
          ) : (
              <div className="main-content__body">
                <div className="query-container">
                  {queryResults}{" "}
                  <div className="fixed-buttons">
                    <Button
                      className="minds-action-btn"
                      type="button"
                      icon="fas fa-question"
                      label="NEW QUERY"
                      onClick={this.showNewQuery}
                    />
                  </div>
                </div>
              </div>
            )}

          {newQueryVisible && (
            <NewQuery
              visible={newQueryVisible}
              onHide={this.hideNewQuery}
              predictorsStore={predictorsStore}
              predictorStore={predictorStore}
              preselectedPredictorName={preselectedPredictorName}
            />
          )}
        </div>
      </ErrorBoundary>
    );
  }
}

export default Query;
