import React from 'react';
import { Paginator } from 'primereact/paginator';
import classNames from 'classnames';
import PageLinks from './pageLinks';
import './index.scss';

class MindsPaginator extends Paginator {
  render() {
    if (!this.props.alwaysShow && this.getPageCount() === 1) {
      return null;
    }
    const className = classNames('minds-paginator p-paginator p-component p-unselectable-text', this.props.className);

    const paginatorElements = [(
      <button
        key="FirstPageLink"
        type="button"
        className=""
        onClick={ this.changePageToFirst }
        disabled={ this.isFirstPage() }
      >
        <span>|&lt;</span>
      </button>
    ), (
      <button
        key="PrevPageLink"
        type="button"
        className=""
        onClick={ this.changePageToPrev }
        disabled={ this.isFirstPage() }
      >
        <span>&lt;</span>
      </button>
    ), (
      <PageLinks
        key="page-links"
        value={ this.updatePageLinks() }
        page={ this.getPage() }
        onClick={ this.onPageLinkClick }
      />
    ), (
      <button
        key="NextPageLink"
        type="button"
        onClick={ this.changePageToNext }
        disabled={ this.isLastPage() }
      >
        <span>&gt;</span>
      </button>
    ), (
      <button
        key="LastPageLink"
        type="button"
        onClick={ this.changePageToLast }
        disabled={ this.isLastPage() }
      >
        <span>&gt;|</span>
      </button>
    )];


    return (
      <div className={ className } style={ this.props.style }>
        { this.props.leftContent }
        <div className="pages">
          { paginatorElements }
        </div>
      </div>
    );
  }
}

export default MindsPaginator;
