export const wrapHistXY = histData => {
  function parse(val) {
    //check if is number or text
    if (isNaN(val)) {
      return val;
    } else {
      //check if is integer
      if (val % 1 === 0) {
        return parseInt(val, 10);
      } else {
        return parseFloat(parseFloat(val).toFixed(2));
      }
    }
  }
  if (!histData || !histData.x || !histData.y) {
    return [];
  }
  let list = [];
  for (let j = 0; j < histData["x"].length; j++) {
    list.push({ x: parse(histData["x"][j]), y: histData["y"][j] });
  }
  list.sort(function (a, b) {
    return a.x < b.x ? -1 : a.x === b.x ? 0 : 1;
  });
  for (let k = 0; k < list.length; k++) {
    histData["x"][k] = list[k].x;
    histData["y"][k] = list[k].y;
  }
  let { x, y } = histData;
  if (
    histData.type === "categorical" &&
    histData.x.length === 2 &&
    histData.y.length === 2 &&
    histData.x[0] === "x" &&
    histData.x[1] === "y"
  ) {
    [x, y] = y;
  }
  return x.map((key, index) => ({ x: key, y: y[index] }));
};

export const lastEl = arr => arr && arr[arr.length - 1];

export const getIconClass = type => {
  switch (type) {
    case "info":
      return "pi-info-circle";
    case "warning":
      return "pi-exclamation-triangle";
    case "error":
      return "pi-times";
    default:
      return "";
  }
};

export const qualityWarnings = (data) => {
  const { unique, empty, bias, outliers, typing } = data;
  const detectDataType = typing && typing.data_type !== 'Text';

  return {
    unique: unique && unique.unique_values === 1 && detectDataType ? true : false,
    empty: empty && empty.empty_percentage > 0.05 && detectDataType  ? true : false,
    bias: bias && bias.entropy < 0.80 && detectDataType && typing.data_type !== "Numeric" ? true  : false,
    outliers: outliers && outliers.outlier_buckets.length > 0 && detectDataType  ? true : false
  }
};

export const filterAxisData = (source, key) => {
  return source.map(item => {
    if (item.hasOwnProperty(key)) {
      return item[key];
    }
    return null;
  });
};

export const utcStringToUSDateTimeString = (str) =>
  new Date(str).toLocaleDateString("en-US") +
  " " +
  new Date(str).toLocaleTimeString("en-US");

export const chunkArray = (arr, size) =>
  arr.map((_, i) =>
    (i % size === 0 ? arr.slice(i, i + size) : null)).filter(Boolean);


export const getMid = (range) => {
  const floatRange = range.map(el => parseFloat(el));
  if (floatRange % 2 === 1) {
    return range[Math.floor(floatRange.length / 2) + 1].toFixed(2);
  }
  const floorIndex = Math.floor(floatRange.length / 2);
  const ceilIndex = Math.floor(floatRange.length / 2);
  const minValue = Math.min(floatRange[floorIndex], floatRange[ceilIndex]);
  const maxValue = Math.max(floatRange[floorIndex], floatRange[ceilIndex]);
  if (!isNaN(minValue + (maxValue - minValue) / 2)) {
    return (minValue + (maxValue - minValue) / 2).toFixed(2).toString();
  }
}

export const getDataByBuckets = (packBand, dataType, data) => {
  const { matrix, predicted, real } = data;
  let tableData = [];

  /*
  *  This snippet code is to generate buckets divide in 10 by default for not show
  *  the whole information and create based an iteration in each array return from the backend
  *  besides. assign each value based to the predicted field  */
  if (dataType === "numeric" && packBand !== "50") {
    const pack = 50 / parseInt(packBand, 10);
    for (let i = 0; i < 50; i += pack) {
      if (getMid(predicted.slice(i, i + pack))) {
        const row = { predictedField: getMid(predicted.slice(i, i + pack)) };
        for (let j = 0; j < 50; j += pack) {
          let count = 0;
          for (let ii = 0; ii < pack; ii++) {
            for (let jj = 0; jj < pack; jj++) {
              if (matrix[i + ii] && matrix[j + jj]) {
                count += matrix[i + ii][j + jj];
              }
            }

            row[getMid(predicted.slice(j, j + pack))] = count;
          }
        }
        tableData.push(row);
      }
    }
  } else {
    tableData = matrix.map((_el, rowIndex) => {
      let result = { predictedField: predicted[rowIndex] };
      if (dataType === "numeric") {
        result = { predictedField: parseFloat(predicted[rowIndex]).toFixed(2) };
      }
      real.forEach((_fieldName, columnIndex) => result[predicted[columnIndex]] = matrix[rowIndex][columnIndex]);
      return result;
    });
  }

  return tableData;
}

export const normalizeDataConfMatrix = (tableData, columns) => {
  const columnsSums = {};
  for (let i = 0; i < columns.length; i++) {
    columnsSums[`${columns[i]}`] = tableData.reduce(
      (prev, next) => prev + next[`${columns[i]}`], 0);
  }

  tableData.forEach((row) => {
    for (let i = 0; i < columns.length; i++) {
      const index = `${columns[i]}`;
      row[index] = parseFloat((row[index] / columnsSums[index]).toFixed(2));
    }
  });

  return tableData;
}

export const messageNotification = (messages, type, title, msg, sticky) => {
  messages.show({severity: type, summary: title, detail: msg, sticky});
};
