import React from 'react';

export const MenuItem = ({ items, onMenuClick, connected }) => {
  const menu = items.reduce((prev, next) => {
    prev.push(
      <li
        key={next.key}
        className={`${next.active ? "minds-menu__active" : ""} ${
          next.disabled ? "minds-menu__disabled" : ""
        }`}
      >
        <a
          className="minds-menu__a"
          role="button"
          onClick={(e) => {
            e.preventDefault();
            if (next.disabled) {
              return;
            }
            onMenuClick(next.key);
          }}
          href="/"
        >
          <div className="minds-menu__a_mainitem">
            {next.icon.type === "FontAwesome" ? (
              <span className="minds-menu__icon">
                <i className={`fas fa-${next.icon.name}`} />
              </span>
            ) : (
              <img className="minds-menu__icon" src={next.icon} alt="" />
            )}
            <span>{next.label}</span>
            {next.label === "Connection" && (
              <span
                className={`connectionStatusDot ${
                  connected ? "connected" : "disconnected"
                }`}
              ></span>
            )}
          </div>
        </a>
      </li>
    );
    return prev;
  }, []);
  return menu;
}
