import React, { Component } from 'react';
import PropTypes from 'prop-types';
import MindsDB from 'mindsdb-js-sdk';
import { Dialog } from 'primereact/dialog';
import { Button } from 'primereact/button';
import { ProgressBar } from 'primereact/progressbar';
import './index.scss';

class UploadWin extends Component {
  static propTypes = {
    visible: PropTypes.bool.isRequired,
    onHide: PropTypes.func.isRequired
  };

  state = {
    file: null,
    uploading: false,
    percentCompleted: 0,
    showFileErrorMessage: false,
  };

  onShow = () => {
    this.setState({
      uploading: false,
      file: null,
      showFileErrorMessage: false,
    });
  };

  onUpload = async () => {
    const { file } = this.state;
    const predictor = MindsDB.Predictor();

    this.setState({ uploading: true });
    try {
      await predictor.upload(file, this.onUploadProgress);
      // window.growl.show({ severity: 'success', detail: 'Predictor uploaded' });
      const { onHide } = this.props;
      onHide(true);
    } catch (e) {
      // window.showError(e);
      this.setState({ uploading: false });
    }
  };

  onUploadProgress = (percentCompleted) => {
    this.setState({ percentCompleted });
  };

  onPickFile = (e) => {
    if (e.target.files && e.target.files.length) {
      const file = e.target.files[0];

      if (file && file.type === 'application/zip') {
        this.setState({ file, showFileErrorMessage: false });
      } else {
        this.setState({ file: null, showFileErrorMessage: true });
      }
    } else {
      this.setState({ file: null });
    }
  };

  setFieldValue = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  render() {
    const {
      file,
      uploading,
      percentCompleted,
      showFileErrorMessage,
    } = this.state;
    const invalid = !file;
    const header = uploading ? 'Uploading predictor' : 'Upload new predictor';
    const emptyCls = file ? '' : 'minds-file-input-value__empty';
    const fileTitle = file ? file.name : 'No file chosen';

    return (
      <Dialog
        visible={ this.props.visible }
        onHide={ this.props.onHide }
        onShow={ this.onShow }
        modal
        header={ header }
        className="minds-upload-source"
      >
        { uploading
          ? <ProgressBar value={ percentCompleted } />
          : (
            <div>
              <div className="p-inputgroup minds-inputgroup">
                <span className="p-inputgroup-addon minds-inputgroup-addon">File:</span>
                <div className={ `minds-file-input-value ${emptyCls}` }>{fileTitle}</div>
                <input
                  title={ fileTitle }
                  placeholder=""
                  type="file"
                  id="file-input"
                  className="minds-file-input mdb-input"
                  accept="zip,application/octet-stream,application/zip,application/x-zip,application/x-zip-compressed"
                  onChange={ this.onPickFile }
                />
              </div>
              {
                showFileErrorMessage
                  ? <p className="error-message">Just .zip files are allowed</p>
                  : null
              }
              <div className="separate-btns">
                <Button
                  className="minds-action-btn red"
                  type="button"
                  label={ (
                    <div className="icon-text">
                      <span>CANCEL</span>
                    </div>
                  ) }
                  onClick={ this.props.onHide }
                />
                <Button
                  className="minds-action-btn"
                  type="button"
                  label={ (
                    <div className="icon-text">
                      <span>UPLOAD</span>
                    </div>
                  ) }
                  onClick={ this.onUpload }
                  disabled={ invalid }
                />
              </div>
            </div>
          )
        }
      </Dialog>
    );
  }
}

export default UploadWin;
