import React from "react";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { Checkbox } from "primereact/checkbox";

import SimpleTable from "../../common/simpleTable";
import "./advancedMode.scss";

const openLink = url => {
  // require("electron").shell.openExternal(url);
};

const validateRequired = (name, value) => {
  if (name === 'marginOfError' && !isNaN(value)) {
    return !value
      || parseFloat(value) === 0
      || (parseFloat(value) >= 0.05 && parseFloat(value) <= 0.2)
  }
}

const AdvancedMode = props => {
  const {
    fields,
    onChange,
    marginOfError,
    stopTrainingAfter,
    backend,
    useGpu,
    removeForTrainingFields
  } = props;

  const onChecked = e => {
    e.target.value = e.checked;
    onChange(e);
  };

  return (
    <div>
      <div className="minds-dialog__field" style={{ flexFlow: "row" }}>
        <div className="field-container" style={{ width: "171px" }}>
          <label htmlFor="in10" className="p-checkbox-label">
            Sample Margin of Error
          </label>
          <InputText
            id="in10"
            name="marginOfError"
            value={marginOfError || 0}
            onChange={onChange}
            className={`mdb-input ${validateRequired('marginOfError', marginOfError) ? '' : 'error'}`}
            placeholder="Default 0 (0.05 - 0.2)"
            keyfilter="pnum"
          />
        </div>

        <div
          className="field-container horizontal-padding"
          style={{ width: "137px" }}
        >
          <label htmlFor="in11" className="p-checkbox-label">
            Stop Training After:
          </label>
          <InputText
            id="in11"
            name="stopTrainingAfter"
            value={stopTrainingAfter || ""}
            onChange={onChange}
            className="mdb-input"
            placeholder="In minutes"
            type="text"
            keyfilter="pint"
          />
        </div>

        <div className="field-container">
          <label htmlFor="in12" className="p-checkbox-label">
            Backend:
          </label>
          <Dropdown
            inputId="in12"
            name="backend"
            value={backend}
            options={[
              { label: "Lightwood", value: "lightwood" },
              { label: "Ludwig", value: "ludwig" }
            ]}
            onChange={onChange}
            placeholder=""
            className="mdb-select"
          />
        </div>
      </div>

      <div
        className="minds-dialog__field"
        style={{ flexFlow: "row", marginTop: "26px" }}
      >
        <div className="flex-container" style={{ marginRight: "50px" }}>
          <label htmlFor="in13" className="p-checkbox-label">
            Use GPU
          </label>
          <Checkbox
            name="useGpu"
            value={useGpu}
            onChange={e => onChecked(e)}
            checked={useGpu}
            className="mdb-select"
          />
        </div>
      </div>

      <div className="minds-dialog__field">
        <label htmlFor="in15" className="p-checkbox-label">
          Select colums to be removed for training:
        </label>
        <SimpleTable
          name="removeForTrainingFields"
          filter
          value={removeForTrainingFields}
          options={fields}
          onChange={onChange}
          useDeleteIcon
        />
      </div>

      <span className="how-to-use">
        For more information on how to use advance parameters{" "}
        <button
          onClick={() =>
            openLink(
              "https://docs.mindsdb.com/PredictorInterface/"
            )
          }
        >
          click here.
        </button>
      </span>
    </div>
  );
};

export default AdvancedMode;
