import React, { useState, useEffect } from 'react';

const DarkMode = () => {
  const [darkMode, setDarkMode] = useState(localStorage.darkMode);

  const darkModeHandle = (e) => {
    if (e && !e.target.checked) {
      localStorage.darkMode = false;
      setDarkMode( localStorage.darkMode);
      document.getElementsByTagName("body")[0].style.filter = "";
    } else if (darkMode === "true" || (e && e.target.checked)) {
      localStorage.setItem("darkMode", true);
      setDarkMode(localStorage.darkMode);
      document.getElementsByTagName("body")[0].style.filter =
        "invert(85%) hue-rotate(180deg)";
    }
  };

  useEffect (() => darkModeHandle())

  return (
    <div className="dm-items sw-position ">
      <span className="dm-tt">Dark Mode:</span>
      <label className="switch">
        <input
          type="checkbox"
          onChange={(e) => darkModeHandle(e)}
          checked={darkMode === "true" && true}
        />
        <span className="slider round" />
      </label>
    </div>
  )
}

export default DarkMode;
