import React, { Component } from "react";
import PropTypes from "prop-types";
import { observer } from "mobx-react";
import "./index.scss";

import Config from "../config";
import Datasources from "../datasources";
import Databases from "../databases";
import DataSourcePreview from "../dataSourcePreview";
import DataSourceQuality from "../dataSourceQuality";
import Predictors from "../predictors";
import PredictorView from "../predictorView";
import PredictorDataQuality from "../predictorDataQuality";
import Query from "../query";
import MindsMenu from "../mindsMenu";

import connectionStore from "../../store/connection";
import dataSourcesStore from "../../store/dataSources";
import dataSourceStore from "../../store/dataSource";
import predictorsStore from "../../store/predictors";
import predictorStore from "../../store/predictor";
import navigation from "../../store/navigation";
import { menuItems } from './menuItems';

import Loading from "../ui/Loading"

@observer
class Main extends Component {
  static propTypes = {
    configStore: PropTypes.object.isRequired
  };

  state = {};

  componentDidMount() {
    setTimeout(() => navigation.setStage("config") , 500)
  }

  setStage = stage => {
    navigation.setStage(stage);
  };

  goToConnect= () => {
    this.setStage("config");
  }

  render() {
    const { stage, predictor, dataSource, queryPredictorName } = navigation;
    const { connected } = connectionStore;
    const { configStore } = this.props;

    const emptyBlock = (
      <div
        style={{
          height: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column"
        }}
      >
          <Loading />
          <p className="empty__version">Version {configStore.config.version}</p>

      </div>
    );
    const items = menuItems(stage, dataSource, connected, predictor);
    return (
      <div className="main-block">
        <MindsMenu
          classUl="main-block__menu"
          items={items}
          onMenuClick={this.setStage}
          connectionStore={connectionStore}
        />
        <div className="main-block__body">
          {stage === "config" && (
            <Config
              dataSourcesStore={dataSourcesStore}
              connectionStore={connectionStore}
              configStore={configStore}
              predictorsStore={predictorsStore}
            />
          )}
          {stage === "datasources" && (
            <Datasources dataSourcesStore={dataSourcesStore} />
          )}
          {stage === "databases" && (
            <Databases dataSourcesStore={dataSourcesStore} />
          )}
          {stage === "dataSourcePreview" && (
            <DataSourcePreview />
          )}
          {stage === "dataSourceQuality" && (
            <DataSourceQuality
              dataSourceStore={dataSourceStore}
              dataSourcesStore={dataSourcesStore}
            />
          )}
          {stage === "predictors" && (
            <Predictors predictorsStore={predictorsStore} />
          )}
          {stage === "predictorView" && (
            <PredictorView
              predictorStore={predictorStore}
              predictor={predictor}
            />
          )}
          {stage === "predictorDataQuality" && <PredictorDataQuality />}
          {stage === "query" && (
            <Query
              dataSource={dataSource}
              preselectedPredictorName={queryPredictorName}
              ref={r => {
                this.queryPanelRef = r;
              }}
            />
          )}
          {stage === "empty" && emptyBlock}
        </div>
      </div>
    );
  }
}

export default Main;
