import React, { useState, useEffect } from 'react';
import * as Yup from 'yup';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import { Dialog } from 'primereact/dialog';
import { InputTitle, QuestionsContainer } from './styles';
import { Dropdown } from 'primereact/dropdown';
import { Button } from 'primereact/button';
import MindsDB from 'mindsdb-js-sdk';
import "./index.scss";

const NeoDBConnectionWin = ({ visible, onHide, isEdit, showMessage }) => {
  const [type, setType] = useState('');
  useEffect(() => {
    setType(isEdit.type);
  }, [isEdit]);
  const formSchema = Yup.object().shape({
    user: Yup.string().required('user is required'),
    password: Yup.string(),
    host: Yup.string().required('host is required'),
    DatabaseName: Yup.string().required('Database name is required'),
    port: Yup.number()
      .typeError('port must be a number')
      .positive('port must be greater than zero')
      .max(65353)
      .required('port is required'),
  });
  const onShow = () => {
    console.log('onShow');
  };
  return (
    <Dialog
      visible={visible}
      onShow={onShow}
      onHide={onHide}
      modal
      header={'Connect to Database'}
      className="minds-database-source"
    >
      <Formik
        initialValues={{
          user: isEdit.user || '',
          password: isEdit.password || '',
          DatabaseName: isEdit.database_name || '',
          port: isEdit.port || '',
          host: isEdit.host || '',
        }}
        validationSchema={formSchema}
        onSubmit={async (values) => {
          const data = {
            params: {
              enabled: true,
              host: values.host,
              password: values.password,
              port: values.port,
              type,
              database_name: values.DatabaseName,
              user: values.user,
            },
          };
          await MindsDB.DataBase().create(data);
          onHide();
          //TODO pending validation error request #24
          showMessage('success', 'Data base created')
        }}
      >
        <Form>
          <QuestionsContainer>
            <div className="p-inputgroup minds-inputgroup">
              <InputTitle htmlFor="UserName">Supported Databases: </InputTitle>
              <Dropdown
                className="mdb-select"
                required
                options={[
                  { label: 'ClickHouse', value: 'clickhouse' },
                  { label: 'MariaDB', value: 'mariadb' },
                ]}
                placeholder="MySQL, MariaDB and more "
                value={type}
                onChange={(e) => setType(e.target.value)}
              />
            </div>
          </QuestionsContainer>
          <QuestionsContainer>
            <div className="p-inputgroup minds-inputgroup">
              <InputTitle>Host:</InputTitle>
              <Field
                className="form-control"
                name="host"
                placeholder="localhost"
                type="text"
              />
              <ErrorMessage
                name="host"
                component="div"
                className="field-error text-danger"
              />
            </div>
            <div className="p-inputgroup minds-inputgroup">
              <InputTitle>Port:</InputTitle>
              <Field
                className="form-control"
                name="port"
                placeholder="3306"
                type="text"
              />
              <ErrorMessage
                name="port"
                component="div"
                className="field-error text-danger"
              />
            </div>
          </QuestionsContainer>
          <div>
            <div className="p-inputgroup minds-inputgroup">
              <InputTitle>Database Name:</InputTitle>
              <Field
                className="form-control"
                name="DatabaseName"
                placeholder="Username"
                type="text"
              />
              <ErrorMessage
                name="DatabaseName"
                component="div"
                className="field-error text-danger"
              />
            </div>
          </div>
          <QuestionsContainer>
            <div className="p-inputgroup minds-inputgroup">
              <InputTitle>Username:</InputTitle>
              <Field
                className="form-control"
                name="user"
                placeholder="root"
                type="text"
              />
              <ErrorMessage
                name="user"
                component="div"
                className="field-error text-danger"
              />
            </div>
            <div className="p-inputgroup minds-inputgroup">
              <InputTitle>Password:</InputTitle>
              <Field
                className="form-control"
                name="password"
                placeholder=""
                type="password"
              />
              <ErrorMessage
                name="password"
                component="div"
                className="field-error text-danger"
              />
            </div>
          </QuestionsContainer>
          <div>
            <div className="separate-btns">
              <Button
                className="minds-action-btn red"
                type="button"
                label={
                  <div className="icon-text">
                    <span>CANCEL</span>
                  </div>
                }
                onClick={onHide}
              />
              <Button
                className="minds-action-btn"
                label={
                  <div className="icon-text">
                    <span>CREATE</span>
                  </div>
                }
                type="submit"
                disabled={false}
              />
            </div>
          </div>
        </Form>
      </Formik>
    </Dialog>
  );
};

export default NeoDBConnectionWin;
