import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import { observer } from 'mobx-react';
import { Button } from 'primereact/button';
import { Mixpanel } from '../../utils/mixpanel';
import { Breadcrumbs } from '../ui/Breadcrumbs';
import { TitleBar } from '../ui/TittleBar';
import Loading from '../ui/Loading'

import MindsPanel from '../common/mindsPanel';
import WarningMark from '../ui/warningMark';

import navigation from '../../store/navigation';
import dataSourceQualityStore from '../../store/dataSourceQuality';

import { qualityWarnings } from '../../utils/funcs';
import './index.scss';

@observer
class DataSourceQuality extends Component {
  state = {};

  goToDatasources = (e) => {
    e.preventDefault();
    e.stopPropagation();
    navigation.setStage('datasources');
  };

  render() {
    if (dataSourceQualityStore.loading === true || dataSourceQualityStore.loaded === false) {
      const currentStatus = dataSourceQualityStore.analyzeStatus === "analyzing" || null
      const loadingProps = {
        message: currentStatus ?
         "This analysis may take some time, depending on the dataset's size and content type."
         : null,
        iconMessage: currentStatus ? "fa-info-circle" : null,
        type: currentStatus ? "blue" : null
      }
      return < Loading {...loadingProps} />
    }

    const targetColumnsMetadata = dataSourceQualityStore.data || [];
    let dataQualityTargetColumns;

    if (targetColumnsMetadata.status !== "analyzing") {
      dataQualityTargetColumns = targetColumnsMetadata["data_analysis_v1"].map((columnMetadata) => {
        columnMetadata.data_analysis_v2 = targetColumnsMetadata["data_analysis_v2"][columnMetadata['column_name']];
        columnMetadata.warnings = qualityWarnings(columnMetadata.data_analysis_v2);
        columnMetadata.warningsLength = Object.keys(columnMetadata.warnings).filter(x => columnMetadata.warnings[x]).length;

        return (
          <>
            {columnMetadata['column_name'] && (
              <MindsPanel
                className="minds-block interactive-title"
                key={`key-${columnMetadata['column_name']}`}
                header={(
                  <div className="collapsed-panelTitle">
                    <span>{columnMetadata['column_name']} </span>
                    {
                      columnMetadata.warningsLength >= 1 && (
                        <div className="warn-collapsed">
                          <span className="low-span">{columnMetadata.warningsLength} Warnings&nbsp;</span>
                          <WarningMark type='yellow' />
                        </div>
                      )
                    }
                  </div>
                )}
                sectionName={columnMetadata['column_name']}
                columnMetadata={columnMetadata}
                toggleable
                collapsed
              />
            )}
          </>
        );
      });
    }

    return (
      <div className="main-content">
        <div className="header">
          <div />
          <TitleBar>
            Datasource Quality
          </TitleBar>
          <Breadcrumbs
            goToDatasources={this.goToDatasources}
            dataSourceId={dataSourceQualityStore.dataSourceName}
            actualSite='Datasources'
          />
        </div>
        <div className="main-content__body datasource-data-quality">
          <div className="data-container">
            {dataQualityTargetColumns}
          </div>
        </div>
        <div className="alert-quality">
          <WarningMark type='blue' icon="fa-info-circle" />
          Data set analysis is designed to be done on a random, representative sample of your total data.
        </div>
        <Button type="button"
          className="minds-action-btn quality-position"
          label="PREVIEW"
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();

            navigation.openDataPreview(dataSourceQualityStore.dataSource);
            Mixpanel.track('Datasources > Preview');
          }}
        />
      </div>
    );
  }
}

export default DataSourceQuality;
