import { observable, computed, action, decorate } from "mobx";
import connectionStore from "./connection";

// const ipcRenderer = require("electron").ipcRenderer;

class ConfigStore {
  config = {
    localServerStatus: {
      installed: false,
      installing: false,
      starting: false,
      running: false,
    },
    waitingLocalServerStatusUpdate: false,
  };

  log = [];

  maxLogLength = 500;

  goodPythonVersion = "3.6";

  cloudUrl = "https://cloud.mindsdb.com";

  firebaseConfig = {
    apiKey: "AIzaSyCIVNYLF-8_yUn8Qxxl4jL0TFJq3HOEmD0",
    authDomain: "mindsdb-cloud.firebaseapp.com",
    databaseURL: "https://mindsdb-cloud.firebaseio.com",
    projectId: "mindsdb-cloud",
    storageBucket: "mindsdb-cloud.appspot.com",
    messagingSenderId: "18876219561",
    appId: "1:18876219561:web:6fa6992c7ef192dc8869b8",
    measurementId: "G-Q6LG33G7SP",
  };

  constructor() {
    // ipcRenderer.on("config", (_e, data) => {
    //   this.updateConfig(data);
    // });

    // ipcRenderer.on("local-server-log", (_e, data) => {
    //   this.appendLog(data);
    // });
  }

  waitLocalServerStatusUpdate() {
    this.config.waitingLocalServerStatusUpdate = true;
  }

  appendLog(logPart) {
    this.log.push(logPart);
    if (this.log.length > this.maxLogLength) {
      this.log = this.log.slice(this.log.length - this.maxLogLength);
    }
  }

  updateConfig(newConfig) {
    if (newConfig.localServerStatus !== undefined) {
      this.config.waitingLocalServerStatusUpdate = false;
      if (
        this.config.localServerStatus.running === false &&
        newConfig.localServerStatus.running === true
      ) {
        const apikey = localStorage.getItem("apikey");

        const params = [{ key: "apikey", value: apikey ? apikey : undefined }];
        connectionStore.connect("http://localhost:47334", params);
      }

      if (
        this.config.localServerStatus.running === true &&
        newConfig.localServerStatus.running === false
      ) {
        connectionStore.disconnect();
      }
    }

    Object.assign(this.config, newConfig);
  }

  saveConfig(newConfig) {
    this.updateConfig(newConfig);
    // ipcRenderer.send("save-config", newConfig);
  }

  get isGoodPython() {
    // compare vith '3.6'
    // if (this.config.python.isPython) {
    //   const goodPythonVersion = this.goodPythonVersion.split(".");
    //   const splitted = this.config.python.version.split(".");
    //   if (splitted.length < 2) return false;
    //   if (parseInt(splitted[0], 10) < goodPythonVersion[0]) return false;
    //   if (parseInt(splitted[1], 10) < goodPythonVersion[1]) return false;
    //   return true;
    // }
    return false;
  }
}

decorate(ConfigStore, {
  config: observable,
  log: observable,
  waitLocalServerStatusUpdate: action,
  appendLog: action,
  updateConfig: action,
  saveConfig: action,
  isGoodPython: computed
});

export default new ConfigStore();
