import React, { Component } from "react";
import EmptyPage from "../emptyPage"
import "./ErrorBoundary.scss";
class ErrorBoundary extends Component {
  state = { hasError: false };

  static getDerivedStateFromError(error) {
    // Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    // You can also log the error to an error reporting service
    console.error(error, errorInfo);
  }

  render() {
    const { fullScreen, fullPosition }  = this.props;

    if (this.state.hasError) {
      // You can render any custom fallback UI
      return (
        <div className={`${ fullScreen ? 'main-content' : ''}`} style={{
          paddingBottom: "6rem",
          overflow: fullScreen ? "hidden" : "auto"
        }}>
          <EmptyPage
            topMessage="Something wrong happened!"
            customIcon="fa-flushed"
            bottomMessage="But we are sure we can explain!"
            customBtnPosition={ fullPosition ? '20% auto' : '0 auto 10px'}
            isHasButton
            noneBorder={!fullScreen}
          />
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;
